import { firebaseConnection } from "../../firebase";
var dataSnapshot = firebaseConnection.database().ref('Table1/BitAdjust');
var dataSnapshotCommand = firebaseConnection.database().ref('Table1/MQTT/SUBSCRIBE_TO_PLC');
var userActiveDataSnapshot = firebaseConnection.database().ref('Table1/UserActive');
