export const actionToggleBitAdjust = (item) => {
    return (dispatch, getState) => {
        let bitAdjust = {
            id: item.id,
            tag: item.tag,
            width: item.width,
            height: item.height,
            top: item.top,
            left: item.left,
            fontSize: item.fontSize,
            text: item.text,
            type: item.type,
        }
        if (getState().auth.editMode || getState().auth.addMode) {
            dispatch({ type: 'OFF_ALL_STATE' })
            dispatch({ type: 'TOGGLE_EDIT_BITADJUST' })
            dispatch({ type: 'TOGGLE_BITADJUST', bitAdjust })
        } else {
            if (item.id !== '') {
                dataSnapshotCommand.child('COMPC').set(getState().element.parentScada + '|BitAdjust|' + item.tag.toString());
                const { auth } = getState().firebase;
                let tempAction = {
                    action: 'BitAdjustToggle',
                    email: auth.email,
                    nameScada: getState().element.parentScada,
                    tag: item.tag,
                    createAt: Date.now()
                }
                userActiveDataSnapshot.push(tempAction);
            }
        }
    }
}

export const actionAddBitAdjust = () => {
    return (dispatch, getState) => {
        const bitAdjust = getState().bitAdjust.addBitAdjust
        let tempBitAdjust = {
            parent: getState().element.parentScada,
            top: parseInt(bitAdjust.top),
            left: parseInt(bitAdjust.left),
            text: bitAdjust.text,
            width: parseInt(bitAdjust.width),
            height: parseInt(bitAdjust.height),
            fontSize: parseInt(bitAdjust.fontSize),
            tag: bitAdjust.tag,
            type: parseInt(bitAdjust.type),
            status: true
        }
        dataSnapshot.push(tempBitAdjust);
        dataSnapshotCommand.child('COMPC').set(getState().element.parentScada + '|Refresh_BitAdjust');
        const { auth } = getState().firebase;
        let tempAction = {
            action: 'BitAdjustAdd',
            email: auth.email,
            nameScada: getState().element.parentScada,
            tag: bitAdjust.tag,
            createAt: Date.now()
        }
        userActiveDataSnapshot.push(tempAction);
        let message = 'Success';
        let status = 'success';
        dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
    }
}
export const actionDeleteBitAdjust = () => {
    return (dispatch, getState) => {
        if (getState().bitAdjust.editBitAdjust.id !== '') {
            dataSnapshot.child(getState().bitAdjust.editBitAdjust.id).remove();
            const { auth } = getState().firebase;
            let tempAction = {
                action: 'BitAdjustDelete',
                email: auth.email,
                nameScada: getState().element.parentScada,
                tag: getState().bitAdjust.editBitAdjust.tag,
                createAt: Date.now()
            }
            userActiveDataSnapshot.push(tempAction);
            dispatch({ type: 'RESET_BITADJUST_EDIT_FORM' })
            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
        }
    }
}
export const actionUpdateBitAdjust = (item) => {
    return (dispatch, getState) => {
        const item = getState().bitAdjust.editBitAdjust
        if (item.id !== '') {
            dataSnapshot.child(item.id).child('tag').set(item.tag);
            dataSnapshot.child(item.id).child('left').set(item.left);
            dataSnapshot.child(item.id).child('top').set(item.top);
            dataSnapshot.child(item.id).child('width').set(item.width);
            dataSnapshot.child(item.id).child('height').set(item.height);
            dataSnapshot.child(item.id).child('fontSize').set(item.fontSize);
            dataSnapshot.child(item.id).child('text').set(item.text);
            dataSnapshot.child(item.id).child('type').set(item.type);

            const { auth } = getState().firebase;
            let tempAction = {
                action: 'BitAdjustUpdate',
                email: auth.email,
                nameScada: getState().element.parentScada,
                tag: item.tag,
                createAt: Date.now()
            }
            userActiveDataSnapshot.push(tempAction);

            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
        }
    }
}
