import React, { Component } from 'react'
import { connect } from 'react-redux';
import * as action from '../../redux/actions/wordAdjustAction'
class InputWordAdjustClick extends Component {
    constructor(props) {
        super(props);
        this.state = {
            text: '',
        }
    }
    isChange = (evt) => {
        const name = evt.target.name;
        const value = evt.target.value;
        this.setState({
            [name]: value
        });
    };
    componentWillMount() {
        this.setState({
            text: this.props.text,
        })
    }
    componentDidUpdate(prevProps, prevState) {
        // only update chart if the data has changed
        if (prevProps.id !== this.props.id) {
            this.setState({
                text: this.props.text,
            })
        }
    }
    handleKeyUp = (event) => {
        if (event.keyCode === 13) {
            this.props.actionWordAdjustEnter({
                id: this.props.id,
                text: this.state.text,
                tag:this.props.tag
            })
        }
        if (event.keyCode === 27) {
            this.props.actionWordAdjustCancel()
        }
    }
    render() {
        const WordAdjustClickStyle = {
            position: 'absolute',
            top: parseInt(this.props.top),
            left: parseInt(this.props.left),
            width: parseInt(this.props.width),
            height: parseInt(this.props.height),
            fontSize: parseInt(this.props.fontSize),
            backgroundColor: 'yellow',
            textAlign: 'center',
            zIndex: 11,
        };
        return (
            <input onClick={this.props.actionInputWordAdjustClick} onKeyDown={this.handleKeyUp} style={WordAdjustClickStyle} value={this.state.text} onChange={(evt) => { this.isChange(evt) }} type="text" className="form-control" name="text" id="text" aria-describedby="helpIdTitle" />
        )
    }
}
const mapStateToProps = (state, ownProps) => {
    return {
        id: state.wordAdjust.clickWordAdjust.id,
        tag: state.wordAdjust.clickWordAdjust.tag,
        left: state.wordAdjust.clickWordAdjust.left,
        top: state.wordAdjust.clickWordAdjust.top,
        text: state.wordAdjust.clickWordAdjust.text,
        width: state.wordAdjust.clickWordAdjust.width,
        height: state.wordAdjust.clickWordAdjust.height,
        fontSize: state.wordAdjust.clickWordAdjust.fontSize,
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        actionInputWordAdjustClick: () => {
            dispatch(action.actionInputWordAdjustClick())
        },
        actionWordAdjustEnter: (item) => {
            dispatch(action.actionWordAdjustEnter(item))
        },
        actionWordAdjustCancel: () => {
            dispatch(action.actionWordAdjustCancel())
        },
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(InputWordAdjustClick)