import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import Button from '@material-ui/core/Button';
import * as actionToggle from '../../../redux/actions/authAction'
import * as action from '../../../redux/actions/textFieldInputAction'
class TextFieldFormAdd extends Component {
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.actionAddTextField();
  };
  handleChangeTag = (e) => {
    e.preventDefault();
    this.props.actionTextFieldAddTagChange(e.target.value);
  };
  handleChangeLabel = (e) => {
    e.preventDefault();
    this.props.actionTextFieldAddLabelChange(e.target.value);
  };
  handleChangeValue = (e) => {
    e.preventDefault();
    this.props.actionTextFieldAddValueChange(e.target.value);
  };
  handleChangeLeft = (e) => {
    e.preventDefault();
    this.props.actionTextFieldAddLeftChange(e.target.value);
  };
  handleChangeTop = (e) => {
    e.preventDefault();
    this.props.actionTextFieldAddTopChange(e.target.value);
  };
  handleChangeWidth = (e) => {
    e.preventDefault();
    this.props.actionTextFieldAddWidthChange(e.target.value);
  };
  handleChangeColor = (e) => {
    e.preventDefault();
    this.props.actionTextFieldAddColorChange(e.target.value);
  };
  handleChangeFontSize = (e) => {
    e.preventDefault();
    this.props.actionTextFieldAddFontSizeChange(e.target.value);
  };
  handleChangeRows = (e) => {
    e.preventDefault();
    this.props.actionTextFieldAddRowsChange(e.target.value);
  };
  handleChangeText = (e) => {
    e.preventDefault();
    this.props.actionTextFieldAddTextChange(e.target.value);
  };

  handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      this.props.actionAddTextField();
    }
  }
  handleKeyPress = (event) => {
    var offsetShift = 0, offsetCtrl = 0, offestDefault = 0;
    if (event.shiftKey) {//shift
      offsetShift = 10; offestDefault = 0;
    } else { offsetShift = 0; }
    if (event.ctrlKey) {//control
      offsetCtrl = 5; offestDefault = 0;
    } else { offsetCtrl = 0; }
    if (!event.shiftKey && !event.ctrlKey) { offestDefault = 1 }
    if (event.keyCode === 37 && (!event.shiftKey || !event.ctrlKey)) {//Trai
      this.props.actionMoveLeftDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 38 && (!event.shiftKey || !event.ctrlKey)) {//Len
      this.props.actionMoveTopDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 39 && (!event.shiftKey || !event.ctrlKey)) {//Phai
      this.props.actionMoveLeftIncrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 40 && (!event.shiftKey || !event.ctrlKey)) {//Xuong
      this.props.actionMoveTopIncrease(offsetShift + offsetCtrl + offestDefault)
    }

    if (event.keyCode === 37 && (event.shiftKey && event.ctrlKey)) {//Trai
      this.props.actionWidthDecrease()
    }
    if (event.keyCode === 39 && (event.shiftKey && event.ctrlKey)) {//Phai
      this.props.actionWidthIncrease()
    }
  }

  render() {
    return (
      <Col sm="12" md="4" lg="3">
        <form autoComplete="off" key='AddLineHorizontal' id="login-form" className="form" onSubmit={this.handleSubmit} onKeyDown={this.handleKeyPress}>
          <div style={{ display: 'grid' }}>
            <Button variant="outlined" color="primary" onClick={this.props.actionToggleEditTextFieldInput}>
              CHANGE EDIT
            </Button>
            <Button variant="outlined" color="secondary" onClick={this.props.actionToggleOffAll}>
              CLOSE
            </Button>
          </div>
          <hr style={{
            color: '#000000',
            backgroundColor: '#000000',
            height: .5,
            borderColor: '#000000'
          }} />

          <h3 className="text-center text-info">ADD TEXT FIELD INPUT</h3>

          <div className="form-group" style={{ width: '100%' }}>
            <input style={{ width: '100%' }} type="submit" name="submit" className="btn btn-info btn-md" value="Add Data" />

            <small className="form-text text-muted">Tag:</small>
            <input type="text" onChange={this.handleChangeTag} placeholder="Tag *" id="Tag" className="form-control" value={this.props.tag} />

            <small className="form-text text-muted">Label:</small>
            <input type="text" onChange={this.handleChangeLabel} placeholder="Label *" id="label" className="form-control" value={this.props.label} />

            <small className="form-text text-muted">Value:</small>
            <input type="text" onChange={this.handleChangeValue} placeholder="Value *" id="text" className="form-control" value={this.props.value} />

            <small className="form-text text-muted">Left:</small>
            <input type="text" onChange={this.handleChangeLeft} placeholder="Left *" id="left" className="form-control" value={this.props.left} />

            <small className="form-text text-muted">Top:</small>
            <input type="text" onChange={this.handleChangeTop} placeholder="Top *" id="top" className="form-control" value={this.props.top} />

            <small className="form-text text-muted">Width:</small>
            <input type="text" onChange={this.handleChangeWidth} placeholder="Width *" id="width" className="form-control" value={this.props.width} />

            <small className="form-text text-muted">Color:</small>
            <input type="color" style={{ width: '100%' }} id="color" onChange={this.handleChangeColor} value={this.props.color} />
            <input type="text" onChange={this.handleChangeColor} placeholder="Color *" id="color" className="form-control" value={this.props.color} />

            <small className="form-text text-muted">FontSize:</small>
            <input type="text" onChange={this.handleChangeFontSize} placeholder="FontSize *" id="fontSize" className="form-control" value={this.props.fontSize} />

            <small className="form-text text-muted">Rows:</small>
            <input type="text" onChange={this.handleChangeRows} placeholder="Rows *" id="fontSize" className="form-control" value={this.props.rows} />

          </div>
          <h7 style={{ color: 'red' }}>Press Shift + Ctrl + Arrow to Change Width and Hight</h7><br />
          <h7 style={{ color: 'red' }}>Press Shift + Arrow to move 10 pixcel </h7><br />
          <h7 style={{ color: 'red' }}>Press Ctrl + Arrow to move 5 pixcel </h7>
        </form>

      </Col>
    )
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
    tag: state.textFieldInput.addTextField.tag,
    label: state.textFieldInput.addTextField.label,
    value: state.textFieldInput.addTextField.value,
    left: state.textFieldInput.addTextField.left,
    top: state.textFieldInput.addTextField.top,
    width: state.textFieldInput.addTextField.width,
    color: state.textFieldInput.addTextField.color,
    fontSize: state.textFieldInput.addTextField.fontSize,
    rows: state.textFieldInput.addTextField.rows,
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionAddTextField: () => {
      dispatch(action.actionAddTextField())
    },
    actionTextFieldAddTagChange: (tag) => {
      dispatch(action.actionTextFieldAddTagChange(tag))
    },
    actionTextFieldAddLeftChange: (left) => {
      dispatch(action.actionTextFieldAddLeftChange(left))
    },
    actionTextFieldAddTopChange: (top) => {
      dispatch(action.actionTextFieldAddLeftChange(top))
    },
    actionTextFieldAddWidthChange: (width) => {
      dispatch(action.actionTextFieldAddWidthChange(width))
    },
    actionWidthDecrease: () => {
      dispatch(action.actionWidthDecrease())
    },
    actionWidthIncrease: () => {
      dispatch(action.actionWidthIncrease())
    },
    actionTextFieldAddColorChange: (color) => {
      dispatch(action.actionTextFieldAddColorChange(color))
    },
    actionTextFieldAddFontSizeChange: (fontSize) => {
      dispatch(action.actionTextFieldAddFontSizeChange(fontSize))
    },
    actionTextFieldAddRowsChange: (rows) => {
      dispatch(action.actionTextFieldAddRowsChange(rows))
    },
    actionTextFieldAddLabelChange: (label) => {
      dispatch(action.actionTextFieldAddLabelChange(label))
    },
    actionTextFieldAddValueChange: (value) => {
      dispatch(action.actionTextFieldAddValueChange(value))
    },

    actionMoveLeftDecrease: (i) => {
      dispatch(action.actionMoveLeftDecrease(i))
    },
    actionMoveLeftIncrease: (i) => {
      dispatch(action.actionMoveLeftIncrease(i))
    },
    actionMoveTopDecrease: (i) => {
      dispatch(action.actionMoveTopDecrease(i))
    },
    actionMoveTopIncrease: (i) => {
      dispatch(action.actionMoveTopIncrease(i))
    },

    actionToggleEditTextFieldInput: () => {
      dispatch(actionToggle.actionToggleEditTextFieldInput())
    },
    actionToggleOffAll: () => {
      dispatch(actionToggle.actionToggleOffAll())
    },
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(TextFieldFormAdd)