import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import Button from '@material-ui/core/Button';
import * as actionToggle from '../../../redux/actions/authAction'
import * as action from '../../../redux/actions/wordAdjustAction'
class WordAdjustFormAdd extends Component {
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.actionAddWordAdjust();
  };
  handleChangeText = (e) => {
    e.preventDefault();
    this.props.actionWordAdjustAddTextChange(e.target.value);
  };
  handleChangeWidth = (e) => {
    e.preventDefault();
    this.props.actionWordAdjustAddWidthChange(e.target.value);
  };
  handleChangeHeight = (e) => {
    e.preventDefault();
    this.props.actionWordAdjustAddHeightChange(e.target.value);
  };
  handleChangeFontSize = (e) => {
    e.preventDefault();
    this.props.actionWordAdjustAddFontSizeChange(e.target.value);
  };
  handleChangeTag = (e) => {
    e.preventDefault();
    this.props.actionWordAdjustAddTagChange(e.target.value);
  };
  handleChangeLeft = (e) => {
    e.preventDefault();
    this.props.actionWordAdjustAddLeftChange(e.target.value);
  };
  handleChangeTop = (e) => {
    e.preventDefault();
    this.props.actionWordAdjustAddTopChange(e.target.value);
  };
  handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      this.props.actionAddWordAdjust();
    }
  }
  handleKeyPress = (event) => {
    var offsetShift = 0, offsetCtrl = 0, offestDefault = 0;
    if (event.shiftKey) {//shift
      offsetShift = 10; offestDefault = 0;
    } else { offsetShift = 0; }
    if (event.ctrlKey) {//control
      offsetCtrl = 5; offestDefault = 0;
    } else { offsetCtrl = 0; }
    if (!event.shiftKey && !event.ctrlKey) { offestDefault = 1 }
    if (event.keyCode === 37 && (!event.shiftKey || !event.ctrlKey)) {//Trai
      this.props.actionMoveLeftDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 38 && (!event.shiftKey || !event.ctrlKey)) {//Len
      this.props.actionMoveTopDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 39 && (!event.shiftKey || !event.ctrlKey)) {//Phai
      this.props.actionMoveLeftIncrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 40 && (!event.shiftKey || !event.ctrlKey)) {//Xuong
      this.props.actionMoveTopIncrease(offsetShift + offsetCtrl + offestDefault)
    }

    if (event.keyCode === 37 && (event.shiftKey && event.ctrlKey)) {//Trai
      this.props.actionWidthDecrease()
    }
    if (event.keyCode === 38 && (event.shiftKey && event.ctrlKey)) {//Len
      this.props.actionHeightDecrease()
    }
    if (event.keyCode === 39 && (event.shiftKey && event.ctrlKey)) {//Phai
      this.props.actionWidthIncrease()
    }
    if (event.keyCode === 40 && (event.shiftKey && event.ctrlKey)) {//Xuong
      this.props.actionHeightIncrease()
    }
  }
  render() {
    return (
      <Col sm="12" md="4" lg="3">
        <form autoComplete="off" key='AddLineHorizontal' id="login-form" className="form" onSubmit={this.handleSubmit} onKeyDown={this.handleKeyPress}>
          <div style={{ display: 'grid' }}>
            <Button variant="outlined" color="primary" onClick={this.props.actionToggleEditWordAdjust}>
              CHANGE EDIT
            </Button>
            <Button variant="outlined" color="secondary" onClick={this.props.actionToggleOffAll}>
              CLOSE
            </Button>
          </div>
          <hr style={{
            color: '#000000',
            backgroundColor: '#000000',
            height: .5,
            borderColor: '#000000'
          }} />

          <h3 className="text-center text-info">ADD WORDADJUST</h3>

          <div className="form-group" style={{ width: '100%' }}>
            <input style={{ width: '100%' }} type="submit" name="submit" className="btn btn-info btn-md" value="Add Data" />
          </div>

          <div className="form-group">
            <small className="form-text text-muted">Tag:</small>
            <input type="text" onChange={this.handleChangeTag} placeholder="Tag *" id="tag" className="form-control" value={this.props.WordAdjustTag} />

            <small className="form-text text-muted">Text:</small>
            <input type="text" onChange={this.handleChangeText} placeholder="Text *" id="text" className="form-control" value={this.props.WordAdjustText} />

            <small className="form-text text-muted">Left:</small>
            <input type="text" onChange={this.handleChangeLeft} placeholder="Left *" id="left" className="form-control" value={this.props.WordAdjustLeft} />

            <small className="form-text text-muted">Top:</small>
            <input type="text" onChange={this.handleChangeTop} placeholder="Top *" id="top" className="form-control" value={this.props.WordAdjustTop} />

            <small className="form-text text-muted">Width:</small>
            <input type="text" onChange={this.handleChangeWidth} placeholder="Width *" id="width" className="form-control" value={this.props.WordAdjustWidth} />

            <small className="form-text text-muted">Height:</small>
            <input type="text" onChange={this.handleChangeHeight} placeholder="Height *" id="height" className="form-control" value={this.props.WordAdjustHeight} />

            <small className="form-text text-muted">Font Size:</small>
            <input type="text" onChange={this.handleChangeFontSize} placeholder="Font Size *" id="fontSize" className="form-control" value={this.props.WordAdjustFontSize} />
          </div>
          <h7 style={{ color: 'red' }}>Press Shift + Ctrl + Arrow to Change Width and Hight</h7><br />
          <h7 style={{ color: 'red' }}>Press Shift + Arrow to move 10 pixcel </h7><br />
          <h7 style={{ color: 'red' }}>Press Ctrl + Arrow to move 5 pixcel </h7>
        </form>
      </Col>
    )
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
    WordAdjustLeft: state.wordAdjust.addWordAdjust.left,
    WordAdjustTop: state.wordAdjust.addWordAdjust.top,
    WordAdjustFontSize: state.wordAdjust.addWordAdjust.fontSize,
    WordAdjustWidth: state.wordAdjust.addWordAdjust.width,
    WordAdjustHeight: state.wordAdjust.addWordAdjust.height,
    WordAdjustTag: state.wordAdjust.addWordAdjust.tag,
    WordAdjustText: state.wordAdjust.addWordAdjust.text,
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionAddWordAdjust: () => {
      dispatch(action.actionAddWordAdjust())
    },
    actionWordAdjustAddTextChange: (item) => {
      dispatch(action.actionWordAdjustAddTextChange(item))
    },
    actionWordAdjustAddTagChange: (item) => {
      dispatch(action.actionWordAdjustAddTagChange(item))
    },
    actionWordAdjustAddLeftChange: (item) => {
      dispatch(action.actionWordAdjustAddLeftChange(item))
    },
    actionWordAdjustAddTopChange: (item) => {
      dispatch(action.actionWordAdjustAddTopChange(item))
    },
    actionWordAdjustAddFontSizeChange: (item) => {
      dispatch(action.actionWordAdjustAddFontSizeChange(item))
    },
    actionWordAdjustAddWidthChange: (item) => {
      dispatch(action.actionWordAdjustAddWidthChange(item))
    },
    actionWordAdjustAddHeightChange: (item) => {
      dispatch(action.actionWordAdjustAddHeightChange(item))
    },
    actionWidthDecrease: () => {
      dispatch(action.actionWidthDecrease())
    },
    actionWidthIncrease: () => {
      dispatch(action.actionWidthIncrease())
    },
    actionHeightDecrease: () => {
      dispatch(action.actionHeightDecrease())
    },
    actionHeightIncrease: () => {
      dispatch(action.actionHeightIncrease())
    },
    actionMoveLeftDecrease: (i) => {
      dispatch(action.actionMoveLeftDecrease(i))
    },
    actionMoveLeftIncrease: (i) => {
      dispatch(action.actionMoveLeftIncrease(i))
    },
    actionMoveTopDecrease: (i) => {
      dispatch(action.actionMoveTopDecrease(i))
    },
    actionMoveTopIncrease: (i) => {
      dispatch(action.actionMoveTopIncrease(i))
    },

    actionToggleEditWordAdjust: () => {
      dispatch(actionToggle.actionToggleEditWordAdjust())
    },
    actionToggleOffAll: () => {
      dispatch(actionToggle.actionToggleOffAll())
    },
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(WordAdjustFormAdd)