import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import Button from '@material-ui/core/Button';
import * as actionToggle from '../../../redux/actions/authAction'
import * as action from '../../../redux/actions/dwordAdjustAction'
class DWordAdjustFormAdd extends Component {
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.actionAddDWordAdjust();
  };
  handleChangeText = (e) => {
    e.preventDefault();
    this.props.actionDWordAdjustAddTextChange(e.target.value);
  };
  handleChangeWidth = (e) => {
    e.preventDefault();
    this.props.actionDWordAdjustAddWidthChange(e.target.value);
  };
  handleChangeHeight = (e) => {
    e.preventDefault();
    this.props.actionDWordAdjustAddHeightChange(e.target.value);
  };
  handleChangeFontSize = (e) => {
    e.preventDefault();
    this.props.actionDWordAdjustAddFontSizeChange(e.target.value);
  };
  handleChangeTag = (e) => {
    e.preventDefault();
    this.props.actionDWordAdjustAddTagChange(e.target.value);
  };
  handleChangeLeft = (e) => {
    e.preventDefault();
    this.props.actionDWordAdjustAddLeftChange(e.target.value);
  };
  handleChangeTop = (e) => {
    e.preventDefault();
    this.props.actionDWordAdjustAddTopChange(e.target.value);
  };
  handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      this.props.actionAddDWordAdjust();
    }
  }
  handleKeyPress = (event) => {
    var offsetShift = 0, offsetCtrl = 0, offestDefault = 0;
    if (event.shiftKey) {//shift
      offsetShift = 10; offestDefault = 0;
    } else { offsetShift = 0; }
    if (event.ctrlKey) {//control
      offsetCtrl = 5; offestDefault = 0;
    } else { offsetCtrl = 0; }
    if (!event.shiftKey && !event.ctrlKey) { offestDefault = 1 }
    if (event.keyCode === 37 && (!event.shiftKey || !event.ctrlKey)) {//Trai
      this.props.actionMoveLeftDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 38 && (!event.shiftKey || !event.ctrlKey)) {//Len
      this.props.actionMoveTopDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 39 && (!event.shiftKey || !event.ctrlKey)) {//Phai
      this.props.actionMoveLeftIncrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 40 && (!event.shiftKey || !event.ctrlKey)) {//Xuong
      this.props.actionMoveTopIncrease(offsetShift + offsetCtrl + offestDefault)
    }

    if (event.keyCode === 37 && (event.shiftKey && event.ctrlKey)) {//Trai
      this.props.actionWidthDecrease()
    }
    if (event.keyCode === 38 && (event.shiftKey && event.ctrlKey)) {//Len
      this.props.actionHeightDecrease()
    }
    if (event.keyCode === 39 && (event.shiftKey && event.ctrlKey)) {//Phai
      this.props.actionWidthIncrease()
    }
    if (event.keyCode === 40 && (event.shiftKey && event.ctrlKey)) {//Xuong
      this.props.actionHeightIncrease()
    }
  }
  render() {
    return (
      <Col sm="12" md="4" lg="3">
        <form autoComplete="off" key='AddLineHorizontal' id="login-form" className="form" onSubmit={this.handleSubmit} onKeyDown={this.handleKeyPress}>
          <div style={{ display: 'grid' }}>
            <Button variant="outlined" color="primary" onClick={this.props.actionToggleEditDWordAdjust}>
              CHANGE EDIT
            </Button>
            <Button variant="outlined" color="secondary" onClick={this.props.actionToggleOffAll}>
              CLOSE
            </Button>
          </div>
          <hr style={{
            color: '#000000',
            backgroundColor: '#000000',
            height: .5,
            borderColor: '#000000'
          }} />

          <h3 className="text-center text-info">ADD DWORDADJUST</h3>
          <div className="form-group" style={{ width: '100%' }}>
            <input style={{ width: '100%' }} type="submit" name="submit" className="btn btn-info btn-md" value="Add Data" />
          </div>
          <div className="form-group">
            <small className="form-text text-muted">Tag:</small>
            <input type="text" onChange={this.handleChangeTag} placeholder="Tag *" id="tag" className="form-control" value={this.props.DWordAdjustTag} />

            <small className="form-text text-muted">Text:</small>
            <input type="text" onChange={this.handleChangeText} placeholder="Text *" id="text" className="form-control" value={this.props.DWordAdjustText} />

            <small className="form-text text-muted">Left:</small>
            <input type="text" onChange={this.handleChangeLeft} placeholder="Left *" id="left" className="form-control" value={this.props.DWordAdjustLeft} />

            <small className="form-text text-muted">Top:</small>
            <input type="text" onChange={this.handleChangeTop} placeholder="Top *" id="top" className="form-control" value={this.props.DWordAdjustTop} />

            <small className="form-text text-muted">Width:</small>
            <input type="text" onChange={this.handleChangeWidth} placeholder="Width *" id="width" className="form-control" value={this.props.DWordAdjustWidth} />

            <small className="form-text text-muted">Height:</small>
            <input type="text" onChange={this.handleChangeHeight} placeholder="Height *" id="height" className="form-control" value={this.props.DWordAdjustHeight} />

            <small className="form-text text-muted">Font Size:</small>
            <input type="text" onChange={this.handleChangeFontSize} placeholder="Font Size *" id="fontSize" className="form-control" value={this.props.DWordAdjustFontSize} />
          </div>
          <h7 style={{ color: 'red' }}>Press Shift + Ctrl + Arrow to Change Width and Hight</h7><br />
          <h7 style={{ color: 'red' }}>Press Shift + Arrow to move 10 pixcel </h7><br />
          <h7 style={{ color: 'red' }}>Press Ctrl + Arrow to move 5 pixcel </h7>
        </form>
      </Col>
    )
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
    DWordAdjustLeft: state.dwordAdjust.addDWordAdjust.left,
    DWordAdjustTop: state.dwordAdjust.addDWordAdjust.top,
    DWordAdjustFontSize: state.dwordAdjust.addDWordAdjust.fontSize,
    DWordAdjustWidth: state.dwordAdjust.addDWordAdjust.width,
    DWordAdjustHeight: state.dwordAdjust.addDWordAdjust.height,
    DWordAdjustTag: state.dwordAdjust.addDWordAdjust.tag,
    DWordAdjustText: state.dwordAdjust.addDWordAdjust.text,
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionAddDWordAdjust: () => {
      dispatch(action.actionAddDWordAdjust())
    },
    actionDWordAdjustAddTextChange: (item) => {
      dispatch(action.actionDWordAdjustAddTextChange(item))
    },
    actionDWordAdjustAddTagChange: (item) => {
      dispatch(action.actionDWordAdjustAddTagChange(item))
    },
    actionDWordAdjustAddLeftChange: (item) => {
      dispatch(action.actionDWordAdjustAddLeftChange(item))
    },
    actionDWordAdjustAddTopChange: (item) => {
      dispatch(action.actionDWordAdjustAddTopChange(item))
    },
    actionDWordAdjustAddFontSizeChange: (item) => {
      dispatch(action.actionDWordAdjustAddFontSizeChange(item))
    },
    actionDWordAdjustAddWidthChange: (item) => {
      dispatch(action.actionDWordAdjustAddWidthChange(item))
    },
    actionDWordAdjustAddHeightChange: (item) => {
      dispatch(action.actionDWordAdjustAddHeightChange(item))
    },
    actionWidthDecrease: () => {
      dispatch(action.actionWidthDecrease())
    },
    actionWidthIncrease: () => {
      dispatch(action.actionWidthIncrease())
    },
    actionHeightDecrease: () => {
      dispatch(action.actionHeightDecrease())
    },
    actionHeightIncrease: () => {
      dispatch(action.actionHeightIncrease())
    },
    actionMoveLeftDecrease: (i) => {
      dispatch(action.actionMoveLeftDecrease(i))
    },
    actionMoveLeftIncrease: (i) => {
      dispatch(action.actionMoveLeftIncrease(i))
    },
    actionMoveTopDecrease: (i) => {
      dispatch(action.actionMoveTopDecrease(i))
    },
    actionMoveTopIncrease: (i) => {
      dispatch(action.actionMoveTopIncrease(i))
    },

    actionToggleEditDWordAdjust: () => {
      dispatch(actionToggle.actionToggleEditDWordAdjust())
    },
    actionToggleOffAll: () => {
      dispatch(actionToggle.actionToggleOffAll())
    },
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(DWordAdjustFormAdd)