import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import Button from '@material-ui/core/Button';
import * as actionToggle from '../../../redux/actions/authAction'
import * as action from '../../../redux/actions/wordAdjustAction'
import List from './List'
const utilizeFocus = () => {
	const ref = React.createRef()
	const setFocus = () => {ref.current &&  ref.current.focus()}
	return {setFocus, ref} 
}

class WordAdjustFormEdit extends Component {
  constructor(props){
    super(props)
    this.input1FocusWordAdjustFormEdit = utilizeFocus()
  }
  componentDidMount(){
    this.input1FocusWordAdjustFormEdit.setFocus()
  }
  componentDidUpdate(prevProps, prevState) {
    // only update chart if the data has changed
    if (prevProps.id !== this.props.id) {
        this.input1FocusWordAdjustFormEdit.setFocus()
    }
  }
  handleChangeText = (e) => {
    e.preventDefault();
    this.props.actionWordAdjustEditTextChange(e.target.value);
  };
  handleChangeWidth = (e) => {
    e.preventDefault();
    this.props.actionWordAdjustEditWidthChange(e.target.value);
  };
  handleChangeHeight = (e) => {
    e.preventDefault();
    this.props.actionWordAdjustEditHeightChange(e.target.value);
  };
  handleChangeFontSize = (e) => {
    e.preventDefault();
    this.props.actionWordAdjustEditFontSizeChange(e.target.value);
  };
  handleChangeTag = (e) => {
    e.preventDefault();
    this.props.actionWordAdjustEditTagChange(e.target.value);
  };
  handleChangeLeft = (e) => {
    e.preventDefault();
    this.props.actionWordAdjustEditLeftChange(e.target.value);
  };
  handleChangeTop = (e) => {
    e.preventDefault();
    this.props.actionWordAdjustEditTopChange(e.target.value);
  };
  handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      this.props.actionUpdateWordAdjust();
    }
  }
  handleKeyPress = (event) => {
    var offsetShift = 0, offsetCtrl = 0, offestDefault = 0;
    if (event.shiftKey) {//shift
      offsetShift = 10; offestDefault = 0;
    } else { offsetShift = 0; }
    if (event.ctrlKey) {//control
      offsetCtrl = 5; offestDefault = 0;
    } else { offsetCtrl = 0; }
    if (!event.shiftKey && !event.ctrlKey) { offestDefault = 1 }
    if (event.keyCode === 37 && (!event.shiftKey || !event.ctrlKey)) {//Trai
      this.props.actionEditMoveLeftDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 38 && (!event.shiftKey || !event.ctrlKey)) {//Len
      this.props.actionEditMoveTopDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 39 && (!event.shiftKey || !event.ctrlKey)) {//Phai
      this.props.actionEditMoveLeftIncrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 40 && (!event.shiftKey || !event.ctrlKey)) {//Xuong
      this.props.actionEditMoveTopIncrease(offsetShift + offsetCtrl + offestDefault)
    }

    if (event.keyCode === 37 && (event.shiftKey && event.ctrlKey)) {//Trai
      this.props.actionEditWidthDecrease()
    }
    if (event.keyCode === 38 && (event.shiftKey && event.ctrlKey)) {//Len
      this.props.actionEditHeightDecrease()
    }
    if (event.keyCode === 39 && (event.shiftKey && event.ctrlKey)) {//Phai
      this.props.actionEditWidthIncrease()
    }
    if (event.keyCode === 40 && (event.shiftKey && event.ctrlKey)) {//Xuong
      this.props.actionEditHeightIncrease()
    }
  }
  render() {
    return (
      <Col style={{ overflow: "auto", height:'960px' }} sm="12" md="4" lg="3">
        <form autoComplete="off" key='AddLineHorizontal' id="login-form" className="form" onSubmit={this.handleSubmit} onKeyDown={this.handleKeyPress} onKeyUp={this.handleKeyUp}>
          <div style={{ display: 'grid' }}>
            <Button variant="outlined" color="primary" onClick={this.props.actionToggleAddWordAdjust}>
              CHANGE ADD
            </Button>
            <Button variant="outlined" color="secondary" onClick={this.props.actionToggleOffAll}>
              CLOSE
            </Button>
          </div>
          <hr style={{
            color: '#000000',
            backgroundColor: '#000000',
            height: .5,
            borderColor: '#000000'
          }} />

          <h3 className="text-center text-info">EDIT WORDADJUST</h3>
          <button type="button" onClick=
            {this.props.actionUpdateWordAdjust}
            className="btn btn-primary btn-block">Update</button>

          <div className="dropdown-divider"></div>

          <button type="button" onClick={this.props.actionDeleteWordAdjust}
            className="btn btn-primary btn-block">DELETE
          </button>
          <div className="form-group">
            <small className="form-text text-muted">ID:</small>
            <input ref={this.input1FocusWordAdjustFormEdit.ref} value={this.props.id} type="text" className="form-control" name="id" id="id" aria-describedby="helpIdTitle" placeholder="ID " />

            <small className="form-text text-muted">Tag:</small>
            <input value={this.props.tag} onChange={this.handleChangeTag} type="text" className="form-control" name="tag" id="tag" aria-describedby="helpIdTitle" placeholder="TAG " />

            <small className="form-text text-muted">Text:</small>
            <input value={this.props.text} onChange={this.handleChangeText} type="text" className="form-control" name="text" id="text" aria-describedby="helpIdTitle" placeholder="Text" />

            <small className="form-text text-muted">Left:</small>
            <input value={this.props.left} onChange={this.handleChangeLeft} type="text" className="form-control" name="left" id="left" aria-describedby="helpIdTitle" placeholder="Left" />

            <small className="form-text text-muted">Top:</small>
            <input value={this.props.top} onChange={this.handleChangeTop} type="text" className="form-control" name="top" id="top" aria-describedby="helpIdTitle" placeholder="Top" />

            <small className="form-text text-muted">Width:</small>
            <input value={this.props.width} onChange={this.handleChangeWidth} type="text" className="form-control" name="width" id="width" aria-describedby="helpIdTitle" placeholder="Width" />

            <small className="form-text text-muted">Height:</small>
            <input value={this.props.height} onChange={this.handleChangeHeight} type="text" className="form-control" name="height" id="height" aria-describedby="helpIdTitle" placeholder="height" />

            <small className="form-text text-muted">FontSize:</small>
            <input value={this.props.fontSize} onChange={this.handleChangeFontSize} type="text" className="form-control" name="fontSize" id="fontSize" aria-describedby="helpIdTitle" placeholder="FontSize" />
          </div>
          <h7 style={{ color: 'red' }}>Press Shift + Ctrl + Arrow to Change Width and Hight</h7><br />
          <h7 style={{ color: 'red' }}>Press Shift + Arrow to move 10 pixcel </h7><br />
          <h7 style={{ color: 'red' }}>Press Ctrl + Arrow to move 5 pixcel </h7>
        </form>
        <List dataFirebaseWordAdjust={this.props.dataFirebaseWordAdjust}/>
      </Col>
    )
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
    left: state.wordAdjust.editWordAdjust.left,
    top: state.wordAdjust.editWordAdjust.top,
    fontSize: state.wordAdjust.editWordAdjust.fontSize,
    width: state.wordAdjust.editWordAdjust.width,
    height: state.wordAdjust.editWordAdjust.height,
    tag: state.wordAdjust.editWordAdjust.tag,
    text: state.wordAdjust.editWordAdjust.text,
    id: state.wordAdjust.editWordAdjust.id,
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionUpdateWordAdjust: () => {
      dispatch(action.actionUpdateWordAdjust())
    },
    actionDeleteWordAdjust: () => {
      dispatch(action.actionDeleteWordAdjust())
    },
    actionWordAdjustEditTextChange: (item) => {
      dispatch(action.actionWordAdjustEditTextChange(item))
    },
    actionWordAdjustEditTagChange: (item) => {
      dispatch(action.actionWordAdjustEditTagChange(item))
    },
    actionWordAdjustEditLeftChange: (item) => {
      dispatch(action.actionWordAdjustEditLeftChange(item))
    },
    actionWordAdjustEditTopChange: (item) => {
      dispatch(action.actionWordAdjustEditTopChange(item))
    },
    actionWordAdjustEditFontSizeChange: (item) => {
      dispatch(action.actionWordAdjustEditFontSizeChange(item))
    },
    actionWordAdjustEditWidthChange: (item) => {
      dispatch(action.actionWordAdjustEditWidthChange(item))
    },
    actionWordAdjustEditHeightChange: (item) => {
      dispatch(action.actionWordAdjustEditHeightChange(item))
    },
    actionEditWidthDecrease: () => {
      dispatch(action.actionEditWidthDecrease())
    },
    actionEditWidthIncrease: () => {
      dispatch(action.actionEditWidthIncrease())
    },
    actionEditHeightDecrease: () => {
      dispatch(action.actionEditHeightDecrease())
    },
    actionEditHeightIncrease: () => {
      dispatch(action.actionEditHeightIncrease())
    },
    actionEditMoveLeftDecrease: (i) => {
      dispatch(action.actionEditMoveLeftDecrease(i))
    },
    actionEditMoveLeftIncrease: (i) => {
      dispatch(action.actionEditMoveLeftIncrease(i))
    },
    actionEditMoveTopDecrease: (i) => {
      dispatch(action.actionEditMoveTopDecrease(i))
    },
    actionEditMoveTopIncrease: (i) => {
      dispatch(action.actionEditMoveTopIncrease(i))
    },

    actionToggleAddWordAdjust: () => {
      dispatch(actionToggle.actionToggleAddWordAdjust())
    },
    actionToggleOffAll: () => {
      dispatch(actionToggle.actionToggleOffAll())
    },
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(WordAdjustFormEdit)