export const actionToggleWordDisplay = (item) => {
    return (dispatch, getState) => {
        let wordDisplay = {
            id: item.id,
            tag: item.tag,
            divide: item.divide,
            width: item.width,
            top: item.top,
            left: item.left,
            fontSize: item.fontSize,
            text: item.text,
        }
        if (getState().auth.editMode || getState().auth.addMode) {
            dispatch({ type: 'OFF_ALL_STATE' })
            dispatch({ type: 'TOGGLE_EDIT_WORDDISPLAY' })
            dispatch({ type: 'TOGGLE_WORDDISPLAY', wordDisplay })
        }
    }
}
export const actionAddWordDisplay = () => {
    return (dispatch, getState) => {
        const wordDisplay = getState().wordDisplay.addWordDisplay
        let tempWordDisplay = {
            parent: getState().element.parentScada,
            top: parseInt(wordDisplay.top),
            left: parseInt(wordDisplay.left),
            text: parseInt(wordDisplay.text),
            width: parseInt(wordDisplay.width),
            fontSize: parseInt(wordDisplay.fontSize),
            tag: parseInt(wordDisplay.tag),
            divide: parseInt(wordDisplay.divide),
        }
        dataSnapshot.push(tempWordDisplay);
        dataSnapshotCommand.child('COMPC').set(getState().element.parentScada + '|Refresh_WordDisplay');
        let message = 'Success';
        let status = 'success';
        dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
    }
}
export const actionDeleteWordDisplay = () => {
    return (dispatch, getState) => {
        const id = getState().wordDisplay.editWordDisplay.id
        if (id !== '') {
            dataSnapshot.child(id).remove();
            dispatch({ type: 'RESET_WORDDISPLAY_EDIT_FORM' })
            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
        }
    }
}
export const actionUpdateWordDisplay = () => {
    return (dispatch, getState) => {
        const item = getState().wordDisplay.editWordDisplay
        if (item.id !== '') {
            dataSnapshot.child(item.id).child('tag').set(item.tag);
            dataSnapshot.child(item.id).child('left').set(item.left);
            dataSnapshot.child(item.id).child('top').set(item.top);
            dataSnapshot.child(item.id).child('width').set(item.width);
            dataSnapshot.child(item.id).child('fontSize').set(item.fontSize);
            dataSnapshot.child(item.id).child('text').set(item.text);
            dataSnapshot.child(item.id).child('divide').set(item.divide);
            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
        }
    }
}
