import React, { Component } from 'react'
import { connect } from 'react-redux';
import Button from '@material-ui/core/Button';
import {actionToggleWordAdjust} from '../../../redux/actions/wordAdjustAction'

class WordAdjust extends Component {
    handleWordAdjustClick=()=>{
        this.props.actionToggleWordAdjust({
            id:this.props.i,
            tag:this.props.tag,
            width:this.props.width,
            height:this.props.height,
            left:this.props.left,
            top:this.props.top,
            fontSize:this.props.fontSize,
            text:this.props.text,
        })
    }
    render() {
    const style={
        position:'absolute',
        left:parseInt(this.props.left),
        top:parseInt(this.props.top),
        padding:'1px',
        color: '#2407ff',
        backgroundColor:'#68b7e8',
        width:parseInt(this.props.width),
        height:parseInt(this.props.height),
        fontSize:parseInt(this.props.fontSize),
        zIndex:10
    }
    return (
            <Button onClick={this.handleWordAdjustClick} variant="flat"  style={style}>
                {this.props.text?this.props.text:''}
            </Button>
        )
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        actionToggleWordAdjust: (item) => {
        dispatch(actionToggleWordAdjust(item))
      },
    }
}
export default connect(null,mapDispatchToProps)(WordAdjust);