import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import Button from '@material-ui/core/Button';
import ArrayImage from '../../Screen/mixing/ArrayImage'
import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from "shards-react";
import * as action from '../../../redux/actions/imageAction'
import * as actionToggle from '../../../redux/actions/authAction'
class ImageFormAdd extends Component {
  constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);
    this.state = { open: false, choise: '' };
  }

  toggle() {
    this.setState(prevState => {
      return { open: !prevState.open };
    });
  }
  choise(e) {
    this.setState({ choise: e })
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.actionAddImage();
  };
  handleChangeTag = (e) => {
    e.preventDefault();
    this.props.actionImageAddTagChange(e.target.value);
  };
  handleChangeNameImage = (e) => {
    e.preventDefault();
    this.props.actionImageAddNameImageChange(e.target.value);
  };
  handleChangeNameElement = (e) => {
    e.preventDefault();
    this.props.actionImageAddNameElementChange(e.target.value);
  };
  handleChangeLeft = (e) => {
    e.preventDefault();
    this.props.actionImageAddLeftChange(e.target.value);
  };
  handleChangeTop = (e) => {
    e.preventDefault();
    this.props.actionImageAddTopChange(e.target.value);
  };
  handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      this.props.actionAddLine();
    }
  }
  handleKeyPress = (event) => {
    var offsetShift = 0, offsetCtrl = 0, offestDefault = 0;
    if (event.shiftKey) {//shift
      offsetShift = 10; offestDefault = 0;
    } else { offsetShift = 0; }
    if (event.ctrlKey) {//control
      offsetCtrl = 5; offestDefault = 0;
    } else { offsetCtrl = 0; }
    if (!event.shiftKey && !event.ctrlKey) { offestDefault = 1 }
    if (event.keyCode === 37) {//Trai
      this.props.actionMoveLeftDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 38) {//Len
      this.props.actionMoveTopDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 39) {//Phai
      this.props.actionMoveLeftIncrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 40) {//Xuong
      this.props.actionMoveTopIncrease(offsetShift + offsetCtrl + offestDefault)
    }
  }
  GetDropDownItem = () => {
    return (ArrayImage.map((image, key) => {
      return (<DropdownItem key={key} onClick={() => this.props.actionImageAddNameImageChange(image.name)}>{image.name}</DropdownItem>)
    }))
  }
  render() {
    return (
      <Col sm="12" md="4" lg="3">
        <form autoComplete="off" key='AddLineHorizontal' id="login-form" className="form" onSubmit={this.handleSubmit}>
          <div style={{ display: 'grid' }}>
            <Button variant="outlined" color="primary" onClick={this.props.actionToggleEditImage}>
              CHANGE EDIT
            </Button>
            <Button variant="outlined" color="secondary" onClick={this.props.actionToggleOffAll}>
              CLOSE
            </Button>
          </div>
          <hr style={{
            color: '#000000',
            backgroundColor: '#000000',
            height: .5,
            borderColor: '#000000'
          }} />

          <h3 className="text-center text-info">ADD IMAGE</h3>
          <div className="form-group" style={{ width: '100%' }}>
            <input style={{ width: '100%' }} type="submit" name="submit" className="btn btn-info btn-md" value="Add Data" />
          </div>
          <div className="form-group">
            <label htmlFor="nameImage" className="text-info">Tag:</label><br />
            <input type="text" onChange={this.handleChangeTag} placeholder="Tag *" id="tag" className="form-control" value={this.props.tag} />
          </div>
          <Dropdown open={this.state.open} toggle={this.toggle} className="d-table" size="sm">
            <DropdownToggle>Choise Name Image</DropdownToggle>
            <DropdownMenu right>
              {this.GetDropDownItem()}
            </DropdownMenu>
          </Dropdown>
          <div className="form-group">
            <label htmlFor="nameImage" className="text-info">Name Image:</label><br />
            <input type="text" onChange={this.handleChangeNameImage} onKeyDown={this.handleKeyPress} placeholder="Name Image *" id="nameImage" className="form-control" value={this.props.nameImage} />
          </div>
          <div className="form-group">
            <label htmlFor="left" className="text-info">Left:</label><br />
            <input type="text" onChange={this.handleChangeLeft} onKeyDown={this.handleKeyPress} placeholder="Left *" id="left" className="form-control" value={this.props.left} />
          </div>
          <div className="form-group">
            <label htmlFor="top" className="text-info">Top:</label><br />
            <input type="text" onChange={this.handleChangeTop} onKeyDown={this.handleKeyPress} placeholder="Top *" id="top" className="form-control" value={this.props.top} />
          </div>
          <div className="form-group">
            <label htmlFor="nameImage" className="text-info">Name Element:</label><br />
            <input type="text" onChange={this.handleChangeNameElement} onKeyDown={this.handleKeyPress} placeholder="Name Element *" id="nameElement" className="form-control" value={this.props.nameElement} />
          </div>
        </form>
      </Col>
    )
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
    left: state.image.addImage.left,
    top: state.image.addImage.top,
    nameImage: state.image.addImage.nameImage,
    nameElement: state.image.addImage.nameElement,
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionAddImage: () => {
      dispatch(action.actionAddImage())
    },
    actionImageAddLeftChange: (left) => {
      dispatch(action.actionImageAddLeftChange(left))
    },
    actionImageAddTopChange: (top) => {
      dispatch(action.actionImageAddTopChange(top))
    },
    actionImageAddTagChange: (tag) => {
      dispatch(action.actionImageAddTagChange(tag))
    },
    actionImageAddNameImageChange: (name) => {
      dispatch(action.actionImageAddNameImageChange(name))
    },
    actionImageAddNameElementChange: (name) => {
      dispatch(action.actionImageAddNameElementChange(name))
    },
    actionMoveLeftDecrease: (item) => {
      dispatch(action.actionMoveLeftDecrease(item))
    },
    actionMoveLeftIncrease: (item) => {
      dispatch(action.actionMoveLeftIncrease(item))
    },
    actionMoveTopDecrease: (item) => {
      dispatch(action.actionMoveTopDecrease(item))
    },
    actionMoveTopIncrease: (item) => {
      dispatch(action.actionMoveTopIncrease(item))
    },

    actionToggleEditImage: () => {
      dispatch(actionToggle.toggleEditImage())
    },
    actionToggleOffAll: () => {
      dispatch(actionToggle.actionToggleOffAll())
    },
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(ImageFormAdd)