import React, { Component } from 'react';
import Item from './Item'
class List extends Component {
    constructor(props) {
        super(props)
        this.state = {
            dataFirebase: []
        }
    }
    getData = () => {
        if (this.props.dataFirebaseDWordDisplay.length > 0) {
            return this.props.dataFirebaseDWordDisplay.map((value, key) => {
                return (
                    <Item
                        key={key}
                        i={value.key}
                        tag={value.tag}
                        left={value.left}
                        top={value.top}
                        text={value.text}
                        fontSize={value.fontSize}
                        width={value.width}
                    />
                )
            })
        }
    };
    render() {
        return (
            <div>
                <div id="noteList" role="tablist" aria-multiselectable="true">
                    {this.getData()}
                </div>
            </div>
        );
    }
}

export default (List);