import React, { Component } from 'react'
import { connect } from 'react-redux';
import { actionToggleBitDisplay } from '../../../redux/actions/bitDisplayAction'

class BitDisplay2 extends Component {
    handleBitDisplayClick = () => {
        this.props.actionToggleBitDisplay({
            id: this.props.i,
            tag: this.props.tag,
            left: this.props.left,
            top: this.props.top,
            idImage: this.props.idImage,
            src: this.props.src,
            nameImage0: this.props.nameImage0,
            nameImage1: this.props.nameImage1,
            type: this.props.type,
            width: this.props.width,
            fontSize: this.props.fontSize,
            text1: this.props.text1,
            text0: this.props.text0,
            color0: this.props.color0,
            color1: this.props.color1,
            blink0:typeof(this.props.blink0)==='undefined'?0:this.props.blink0,
            blink1:typeof(this.props.blink1)==='undefined'?0:this.props.blink1,
        })
    }
    render() {
        const style = {
            position: 'absolute',
            left: parseInt(this.props.left),
            top: parseInt(this.props.top),
            width: parseInt(this.props.width),
            zIndex: 10,
        }
        const src = parseInt(this.props.status) ? this.props.url1 : this.props.url0
        if (parseInt(this.props.status) === 1) {
            if (parseInt(this.props.blink1) === 1) {
                return (
                    <img className={'blink'} onClick={this.handleBitDisplayClick} alt='' src={src} style={style} />
                )
            } else {
                return (
                    <img onClick={this.handleBitDisplayClick} alt='' src={src} style={style} />
                )
            }
        } else {
            if (parseInt(this.props.blink0) === 1) {
                return (
                    <img className={'blink'} onClick={this.handleBitDisplayClick} alt='' src={src} style={style} />
                )
            } else {
                return (
                    <img onClick={this.handleBitDisplayClick} alt='' src={src} style={style} />
                )
            }
        }

    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        actionToggleBitDisplay: (item) => {
            dispatch(actionToggleBitDisplay(item))
        },
    }
}
export default connect(null, mapDispatchToProps)(BitDisplay2);