import React, { Component } from 'react'
import { connect } from 'react-redux';
import Button from '@material-ui/core/Button';
import {actionToggleDWordAdjust} from '../../../redux/actions/dwordAdjustAction'

class DWordAdjust extends Component {
    handleDWordAdjustClick=()=>{
        this.props.actionToggleDWordAdjust({
            id:this.props.i,
            tag:this.props.tag,
            width:this.props.width,
            height:this.props.height,
            left:this.props.left,
            top:this.props.top,
            fontSize:this.props.fontSize,
            text:this.props.text,
            zIndex:10
        })
    }
    render() {
    const style={
        position:'absolute',
        left:parseInt(this.props.left),
        top:parseInt(this.props.top),
        padding:'1px',
        color: '#2407ff',
        backgroundColor: '#08d1d1',
        width:parseInt(this.props.width),
        height:parseInt(this.props.height),
        fontSize:parseInt(this.props.fontSize),
        zIndex: 10,
    }
    return (
            <Button onClick={this.handleDWordAdjustClick} variant="outlined"  style={style}>
                {this.props.text?this.props.text:''}
            </Button>
        )
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        actionToggleDWordAdjust: (item) => {
        dispatch(actionToggleDWordAdjust(item))
      },
    }
}
export default connect(null,mapDispatchToProps)(DWordAdjust);