import React, { Component } from 'react'
import { connect } from 'react-redux';
class LabelAdd extends Component {
    render() {
        const labelStyle = {
            position:'absolute',
            top: parseInt(this.props.top),
            left:parseInt(this.props.left),
            fontWeight: 'bold',
            fontSize:parseInt(this.props.fontSize),
            width:parseInt(this.props.width),
            color:this.props.color,
            zIndex:100,
            textAlign:'center'
        };
    return (
            <div style={labelStyle}>{this.props.text}</div>
        )
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        
    }
}
export default connect(null,mapDispatchToProps)(LabelAdd)