import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import Button from '@material-ui/core/Button';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import * as actionToggle from '../../../redux/actions/authAction'
import * as action from '../../../redux/actions/wordDisplayAction'
class WordDisplayFormAdd extends Component {
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.actionAddWordDisplay();
  };
  handleChangeText = (e) => {
    e.preventDefault();
    this.props.actionWordDisplayAddTextChange(e.target.value);
  };
  handleChangeWidth = (e) => {
    e.preventDefault();
    this.props.actionWordDisplayAddWidthChange(e.target.value);
  };
  handleChangeFontSize = (e) => {
    e.preventDefault();
    this.props.actionWordDisplayAddFontSizeChange(e.target.value);
  };
  handleChangeTag = (e) => {
    e.preventDefault();
    this.props.actionWordDisplayAddTagChange(e.target.value);
  };
  handleChangeLeft = (e) => {
    e.preventDefault();
    this.props.actionWordDisplayAddLeftChange(e.target.value);
  };
  handleChangeTop = (e) => {
    e.preventDefault();
    this.props.actionWordDisplayAddTopChange(e.target.value);
  };
  handleChangeDivide = (e) => {
    e.preventDefault();
    this.props.actionWordDisplayAddDivideChange(e.target.value);
  };
  handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      this.props.actionAddWordDisplay();
    }
  }
  handleKeyPress = (event) => {
    var offsetShift = 0, offsetCtrl = 0, offestDefault = 0;
    if (event.shiftKey) {//shift
      offsetShift = 10; offestDefault = 0;
    } else { offsetShift = 0; }
    if (event.ctrlKey) {//control
      offsetCtrl = 5; offestDefault = 0;
    } else { offsetCtrl = 0; }
    if (!event.shiftKey && !event.ctrlKey) { offestDefault = 1 }
    if (event.keyCode === 37 && (!event.shiftKey || !event.ctrlKey)) {//Trai
      this.props.actionMoveLeftDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 38 && (!event.shiftKey || !event.ctrlKey)) {//Len
      this.props.actionMoveTopDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 39 && (!event.shiftKey || !event.ctrlKey)) {//Phai
      this.props.actionMoveLeftIncrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 40 && (!event.shiftKey || !event.ctrlKey)) {//Xuong
      this.props.actionMoveTopIncrease(offsetShift + offsetCtrl + offestDefault)
    }

    if (event.keyCode === 37 && (event.shiftKey && event.ctrlKey)) {//Trai
      this.props.actionWidthDecrease()
    }
    if (event.keyCode === 39 && (event.shiftKey && event.ctrlKey)) {//Phai
      this.props.actionWidthIncrease()
    }
  }
  render() {
    return (
      <Col sm="12" md="4" lg="3">
        <form autoComplete="off" key='AddLineHorizontal' id="login-form" className="form" onSubmit={this.handleSubmit} onKeyDown={this.handleKeyPress}>
          <div style={{ display: 'grid' }}>
            <Button variant="outlined" color="primary" onClick={this.props.actionToggleEditWordDisplay}>
              CHANGE EDIT
            </Button>
            <Button variant="outlined" color="secondary" onClick={this.props.actionToggleOffAll}>
              CLOSE
            </Button>
          </div>
          <hr style={{
            color: '#000000',
            backgroundColor: '#000000',
            height: .5,
            borderColor: '#000000'
          }} />

          <h3 className="text-center text-info">ADD WORDDISPLAY</h3>

          <div className="form-group" style={{ width: '100%' }}>
            <input style={{ width: '100%' }} type="submit" name="submit" className="btn btn-info btn-md" value="Add Data" />
          </div>

          <div className="form-group">
            <small className="form-text text-muted">Tag:</small>
            <input type="text" onChange={this.handleChangeTag} placeholder="Tag *" id="tag" className="form-control" value={this.props.WordDisplayTag} />

            <small className="form-text text-muted">Text:</small>
            <input type="text" onChange={this.handleChangeText} placeholder="Text *" id="text" className="form-control" value={this.props.WordDisplayText} />

            <small className="form-text text-muted">Left:</small>
            <input type="text" onChange={this.handleChangeLeft} placeholder="Left *" id="left" className="form-control" value={this.props.WordDisplayLeft} />

            <small className="form-text text-muted">Top:</small>
            <input type="text" onChange={this.handleChangeTop} placeholder="Top *" id="top" className="form-control" value={this.props.WordDisplayTop} />

            <small className="form-text text-muted">Width:</small>
            <input type="text" onChange={this.handleChangeWidth} placeholder="Width *" id="width" className="form-control" value={this.props.WordDisplayWidth} />

            <small className="form-text text-muted">Font Size:</small>
            <input type="text" onChange={this.handleChangeFontSize} placeholder="Font Size *" id="fontSize" className="form-control" value={this.props.WordDisplayFontSize} />
          </div>
          <RadioGroup aria-label="divide" name="divide" value={this.props.WordDisplayDivide} onChange={this.handleChangeDivide} row>
            <FormControlLabel
              value={1}
              control={<Radio color="primary" />}
              label="0."
              labelPlacement="start"
            />
            <FormControlLabel
              value={10}
              control={<Radio color="primary" />}
              label=".0"
              labelPlacement="start"
            />
            <FormControlLabel
              value={100}
              control={<Radio color="primary" />}
              label=".00"
              labelPlacement="start"
            />
            <FormControlLabel
              value={1000}
              control={<Radio color="primary" />}
              label=".000"
              labelPlacement="start"
            />
          </RadioGroup>
          <h7 style={{ color: 'red' }}>Press Shift + Ctrl + Arrow to Change Width and Hight</h7><br />
          <h7 style={{ color: 'red' }}>Press Shift + Arrow to move 10 pixcel </h7><br />
          <h7 style={{ color: 'red' }}>Press Ctrl + Arrow to move 5 pixcel </h7>
        </form>
      </Col>
    )
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
    WordDisplayLeft: state.wordDisplay.addWordDisplay.left,
    WordDisplayTop: state.wordDisplay.addWordDisplay.top,
    WordDisplayFontSize: state.wordDisplay.addWordDisplay.fontSize,
    WordDisplayWidth: state.wordDisplay.addWordDisplay.width,
    WordDisplayTag: state.wordDisplay.addWordDisplay.tag,
    WordDisplayDivide: parseInt(state.wordDisplay.addWordDisplay.Divide),
    WordDisplayText: state.wordDisplay.addWordDisplay.text,
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionAddWordDisplay: () => {
      dispatch(action.actionAddWordDisplay())
    },
    actionWordDisplayAddTextChange: (item) => {
      dispatch(action.actionWordDisplayAddTextChange(item))
    },
    actionWordDisplayAddTagChange: (item) => {
      dispatch(action.actionWordDisplayAddTagChange(item))
    },
    actionWordDisplayAddLeftChange: (item) => {
      dispatch(action.actionWordDisplayAddLeftChange(item))
    },
    actionWordDisplayAddTopChange: (item) => {
      dispatch(action.actionWordDisplayAddTopChange(item))
    },
    actionWordDisplayAddFontSizeChange: (item) => {
      dispatch(action.actionWordDisplayAddFontSizeChange(item))
    },
    actionWordDisplayAddWidthChange: (item) => {
      dispatch(action.actionWordDisplayAddWidthChange(item))
    },
    actionWordDisplayAddDivideChange: (item) => {
      dispatch(action.actionWordDisplayAddDivideChange(item))
    },

    actionWidthDecrease: () => {
      dispatch(action.actionWidthDecrease())
    },
    actionWidthIncrease: () => {
      dispatch(action.actionWidthIncrease())
    },
    actionMoveLeftDecrease: (i) => {
      dispatch(action.actionMoveLeftDecrease(i))
    },
    actionMoveLeftIncrease: (i) => {
      dispatch(action.actionMoveLeftIncrease(i))
    },
    actionMoveTopDecrease: (i) => {
      dispatch(action.actionMoveTopDecrease(i))
    },
    actionMoveTopIncrease: (i) => {
      dispatch(action.actionMoveTopIncrease(i))
    },

    actionToggleEditWordDisplay: () => {
      dispatch(actionToggle.actionToggleEditWordDisplay())
    },
    actionToggleOffAll: () => {
      dispatch(actionToggle.actionToggleOffAll())
    },
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(WordDisplayFormAdd)