import React, { Component } from 'react'
import { FormCheckbox } from "shards-react";

class BitAdjustToggleAdd4 extends Component {
    constructor() {
        super();
        this.state = { checked: false };
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(checked) {
        this.setState({ checked });
    }
    render() {
        const styleBitAdjust = {
            position: 'absolute',
            left: parseInt(this.props.left),
            top: parseInt(this.props.top),
            zIndex: 10,
        }
        return (
            <div style={styleBitAdjust}>
                <FormCheckbox toggle small inline
                    checked={true}
                    onChange={this.handleChange}>
                    {this.props.text}
                </FormCheckbox>
            </div>
        );
    }
}
export default (BitAdjustToggleAdd4);