import React, { Component } from 'react'
import { connect } from 'react-redux'
import { actionToggleDWordDisplay } from '../../../redux/actions/dwordDisplayAction'
import { Button } from 'reactstrap';
class LabelItem extends Component {
    handleClick = () => {
        this.props.actionToggleDWordDisplay({
            id: this.props.i,
            tag: this.props.tag,
            width: this.props.width,
            left: this.props.left,
            top: this.props.top,
            fontSize: this.props.fontSize,
            text: this.props.text,
            zIndex: 10
        })
    }
    render() {
        const { i, tag, text, left, top } = this.props;
        return (
            <div>
                <div className="card">
                    <Button color="primary" className="px-4" onClick={this.handleClick}>Choose</Button>
                    <div className="card-header" id="note1">
                        <h6 className="mb-0">
                            <a data-toggle="collapse" data-parent="#noteList" href={"#noteContent" + i} aria-expanded="true" aria-controls="noteContent1">
                                tag: {tag} - text: {text}
                            </a>
                        </h6>
                    </div>
                    <div id={"noteContent" + i} className="collapse in" role="tabpanel" aria-labelledby="note1">
                        <div className="card-body">
                            Left:{left} - Top:{top}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
const mapStateToProps = (state, ownProps) => {
    return {
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        actionToggleDWordDisplay: (i) => {
            dispatch(actionToggleDWordDisplay(i))
        },
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(LabelItem);