import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import Button from '@material-ui/core/Button';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import * as actionToggle from '../../../redux/actions/authAction'
import * as action from '../../../redux/actions/wordDisplayAction'
import List from './List'
const utilizeFocus = () => {
	const ref = React.createRef()
	const setFocus = () => {ref.current &&  ref.current.focus()}
	return {setFocus, ref} 
}

class WordDisplayFormEdit extends Component {
  constructor(props){
    super(props)
    this.input1FocusWordDisplayFormEdit = utilizeFocus()
  }
  componentDidMount(){
    this.input1FocusWordDisplayFormEdit.setFocus()
  }
  componentDidUpdate(prevProps, prevState) {
    // only update chart if the data has changed
    if (prevProps.id !== this.props.id) {
        this.input1FocusWordDisplayFormEdit.setFocus()
    }
  }
  handleChangeText = (e) => {
    e.preventDefault();
    this.props.actionWordDisplayEditTextChange(e.target.value);
  };
  handleChangeWidth = (e) => {
    e.preventDefault();
    this.props.actionWordDisplayEditWidthChange(e.target.value);
  };
  handleChangeFontSize = (e) => {
    e.preventDefault();
    this.props.actionWordDisplayEditFontSizeChange(e.target.value);
  };
  handleChangeTag = (e) => {
    e.preventDefault();
    this.props.actionWordDisplayEditTagChange(e.target.value);
  };
  handleChangeLeft = (e) => {
    e.preventDefault();
    this.props.actionWordDisplayEditLeftChange(e.target.value);
  };
  handleChangeTop = (e) => {
    e.preventDefault();
    this.props.actionWordDisplayEditTopChange(e.target.value);
  };
  handleChangeDivide = (e) => {
    e.preventDefault();
    this.props.actionWordDisplayEditDivideChange(e.target.value);
  };
  handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      this.props.actionUpdateWordDisplay();
    }
  }
  handleKeyPress = (event) => {
    var offsetShift = 0, offsetCtrl = 0, offestDefault = 0;
    if (event.shiftKey) {//shift
      offsetShift = 10; offestDefault = 0;
    } else { offsetShift = 0; }
    if (event.ctrlKey) {//control
      offsetCtrl = 5; offestDefault = 0;
    } else { offsetCtrl = 0; }
    if (!event.shiftKey && !event.ctrlKey) { offestDefault = 1 }
    if (event.keyCode === 37 && (!event.shiftKey || !event.ctrlKey)) {//Trai
      this.props.actionEditMoveLeftDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 38 && (!event.shiftKey || !event.ctrlKey)) {//Len
      this.props.actionEditMoveTopDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 39 && (!event.shiftKey || !event.ctrlKey)) {//Phai
      this.props.actionEditMoveLeftIncrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 40 && (!event.shiftKey || !event.ctrlKey)) {//Xuong
      this.props.actionEditMoveTopIncrease(offsetShift + offsetCtrl + offestDefault)
    }

    if (event.keyCode === 37 && (event.shiftKey && event.ctrlKey)) {//Trai
      this.props.actionEditWidthDecrease()
    }
    if (event.keyCode === 39 && (event.shiftKey && event.ctrlKey)) {//Phai
      this.props.actionEditWidthIncrease()
    }
  }
  render() {
    return (
      <Col style={{ overflow: "auto", height:'960px' }} sm="12" md="4" lg="3">
        <form autoComplete="off" onKeyDown={this.handleKeyPress} onKeyUp={this.handleKeyUp} key='AddLineHorizontal' id="login-form" className="form" onSubmit={this.handleSubmit}>
          <div style={{ display: 'grid' }}>
            <Button variant="outlined" color="primary" onClick={this.props.actionToggleAddWordDisplay}>
              CHANGE ADD
            </Button>
            <Button variant="outlined" color="secondary" onClick={this.props.actionToggleOffAll}>
              CLOSE
            </Button>
          </div>
          <hr style={{
            color: '#000000',
            backgroundColor: '#000000',
            height: .5,
            borderColor: '#000000'
          }} />

          <h3 className="text-center text-info">EDIT WORDDISPLAY</h3>
          <button type="button" onClick=
            {this.props.actionUpdateWordDisplay}
            className="btn btn-primary btn-block">Update</button>

          <div className="dropdown-divider"></div>

          <button type="button" onClick={this.props.actionDeleteWordDisplay}
            className="btn btn-primary btn-block">DELETE
                </button>
          <div className="form-group">
            <small className="form-text text-muted">ID:</small>
            <input ref={this.input1FocusWordDisplayFormEdit.ref} value={this.props.id} type="text" className="form-control" name="id" id="id" aria-describedby="helpIdTitle" placeholder="ID " />

            <small className="form-text text-muted">Tag:</small>
            <input value={this.props.tag} onChange={this.handleChangeTag} type="text" className="form-control" name="tag" id="tag" aria-describedby="helpIdTitle" placeholder="TAG " />

            <small className="form-text text-muted">Text:</small>
            <input value={this.props.text} onChange={this.handleChangeText} type="text" className="form-control" name="text" id="text" aria-describedby="helpIdTitle" placeholder="Text" />

            <small className="form-text text-muted">Left:</small>
            <input value={this.props.left} onChange={this.handleChangeLeft} type="text" className="form-control" name="left" id="left" aria-describedby="helpIdTitle" placeholder="Left" />

            <small className="form-text text-muted">Top:</small>
            <input value={this.props.top} onChange={this.handleChangeTop} type="text" className="form-control" name="top" id="top" aria-describedby="helpIdTitle" placeholder="Top" />

            <small className="form-text text-muted">Width:</small>
            <input value={this.props.width} onChange={this.handleChangeWidth} type="text" className="form-control" name="width" id="width" aria-describedby="helpIdTitle" placeholder="Width" />

            <small className="form-text text-muted">FontSize:</small>
            <input value={this.props.fontSize} onChange={this.handleChangeFontSize} type="text" className="form-control" name="fontSize" id="fontSize" aria-describedby="helpIdTitle" placeholder="FontSize" />
          </div>
          <RadioGroup aria-label="divide" name="divide" value={this.props.divide} onChange={this.handleChangeDivide} row>
            <FormControlLabel
              value={1}
              control={<Radio color="primary" />}
              label="0."
              labelPlacement="start"
            />
            <FormControlLabel
              value={10}
              control={<Radio color="primary" />}
              label=".0"
              labelPlacement="start"
            />
            <FormControlLabel
              value={100}
              control={<Radio color="primary" />}
              label=".00"
              labelPlacement="start"
            />
            <FormControlLabel
              value={1000}
              control={<Radio color="primary" />}
              label=".000"
              labelPlacement="start"
            />
          </RadioGroup>
          <h7 style={{ color: 'red' }}>Press Shift + Ctrl + Arrow to Change Width and Hight</h7><br />
          <h7 style={{ color: 'red' }}>Press Shift + Arrow to move 10 pixcel </h7><br />
          <h7 style={{ color: 'red' }}>Press Ctrl + Arrow to move 5 pixcel </h7>
        </form>
        <List dataFirebaseWordDisplay={this.props.dataFirebaseWordDisplay}/>
      </Col>
    )
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
    left: state.wordDisplay.editWordDisplay.left,
    top: state.wordDisplay.editWordDisplay.top,
    fontSize: state.wordDisplay.editWordDisplay.fontSize,
    width: state.wordDisplay.editWordDisplay.width,
    tag: state.wordDisplay.editWordDisplay.tag,
    divide: parseInt(state.wordDisplay.editWordDisplay.divide),
    text: state.wordDisplay.editWordDisplay.text,
    id: state.wordDisplay.editWordDisplay.id,
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionUpdateWordDisplay: () => {
      dispatch(action.actionUpdateWordDisplay())
    },
    actionDeleteWordDisplay: () => {
      dispatch(action.actionDeleteWordDisplay())
    },
    actionWordDisplayEditTextChange: (item) => {
      dispatch(action.actionWordDisplayEditTextChange(item))
    },
    actionWordDisplayEditTagChange: (item) => {
      dispatch(action.actionWordDisplayEditTagChange(item))
    },
    actionWordDisplayEditLeftChange: (item) => {
      dispatch(action.actionWordDisplayEditLeftChange(item))
    },
    actionWordDisplayEditTopChange: (item) => {
      dispatch(action.actionWordDisplayEditTopChange(item))
    },
    actionWordDisplayEditFontSizeChange: (item) => {
      dispatch(action.actionWordDisplayEditFontSizeChange(item))
    },
    actionWordDisplayEditWidthChange: (item) => {
      dispatch(action.actionWordDisplayEditWidthChange(item))
    },
    actionWordDisplayEditDivideChange: (item) => {
      dispatch(action.actionWordDisplayEditDivideChange(item))
    },
    actionEditWidthDecrease: () => {
      dispatch(action.actionEditWidthDecrease())
    },
    actionEditWidthIncrease: () => {
      dispatch(action.actionEditWidthIncrease())
    },
    actionEditMoveLeftDecrease: (i) => {
      dispatch(action.actionEditMoveLeftDecrease(i))
    },
    actionEditMoveLeftIncrease: (i) => {
      dispatch(action.actionEditMoveLeftIncrease(i))
    },
    actionEditMoveTopDecrease: (i) => {
      dispatch(action.actionEditMoveTopDecrease(i))
    },
    actionEditMoveTopIncrease: (i) => {
      dispatch(action.actionEditMoveTopIncrease(i))
    },

    actionToggleAddWordDisplay: () => {
      dispatch(actionToggle.actionToggleAddWordDisplay())
    },
    actionToggleOffAll: () => {
      dispatch(actionToggle.actionToggleOffAll())
    },
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(WordDisplayFormEdit)