import { firebaseConnection } from "../../firebase";
var dataSnapshotCommand = firebaseConnection.database().ref('Table1/MQTT/SUBSCRIBE_TO_PLC');
var dataSnapshot = firebaseConnection.database().ref('Table1/WordAdjust');

export const actionInputWordAdjustClick = () => {
    return (dispatch, getState) => {
        dispatch({ type: 'BACKGROUND_WORDADJUST_CLICK' })
    }
}
export const actionWordAdjustCancel = () => {
    return (dispatch, getState) => {
        dispatch({ type: 'BACKGROUND_CLICK' })
    }
}
export const actionToggleWordAdjust = (item) => {
    return (dispatch, getState) => {
        let wordAdjust = {
            id: item.id,
            tag: item.tag,
            width: item.width,
            height: item.height,
            top: item.top,
            left: item.left,
            fontSize: item.fontSize,
            text: item.text,
        }
        if (getState().auth.editMode || getState().auth.addMode) {
            dispatch({ type: 'OFF_ALL_STATE' })
            dispatch({ type: 'TOGGLE_EDIT_WORDADJUST' })
            dispatch({ type: 'TOGGLE_WORDADJUST', wordAdjust })
        } else {
            dispatch({ type: 'CLICK_WORDADJUST', wordAdjust })
            dispatch({ type: 'BACKGROUND_WORDADJUST_CLICK' })
        }
    }
}
export const actionWordAdjustEnter = (item) => {
    return (dispatch, getState) => {
        if (item.id !== '') {
            dataSnapshotCommand.child('COMPC').set(getState().element.parentScada + '|WordAdjust|' + item.tag.toString() + '|' + item.text);
        }
        dispatch({ type: 'BACKGROUND_CLICK' })
    }
}
export const actionAddWordAdjust = () => {
    return (dispatch, getState) => {
        const wordAdjust = getState().wordAdjust.addWordAdjust
        let tempWordAdjust = {
            parent: getState().element.parentScada,
            top: parseInt(wordAdjust.top),
            left: parseInt(wordAdjust.left),
            text: wordAdjust.text,
            width: parseInt(wordAdjust.width),
            height: parseInt(wordAdjust.height),
            fontSize: parseInt(wordAdjust.fontSize),
            tag: wordAdjust.tag,
        }
        dataSnapshot.push(tempWordAdjust);
        dataSnapshotCommand.child('COMPC').set(getState().element.parentScada + '|Refresh_WordAdjust');
        let message = 'Success';
        let status = 'success';
        dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
    }
}
export const actionDeleteWordAdjust = () => {
    return (dispatch, getState) => {
        const id = getState().wordAdjust.editWordAdjust.id
        if (id !== '') {
            dataSnapshot.child(id).remove();
            dispatch({ type: 'RESET_WORDADJUST_EDIT_FORM' })
            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
        }
    }
}
export const actionUpdateWordAdjust = () => {
    return (dispatch, getState) => {
        const item = getState().wordAdjust.editWordAdjust
        if (item.id !== '') {
            dataSnapshot.child(item.id).child('tag').set(item.tag);
            dataSnapshot.child(item.id).child('left').set(item.left);
            dataSnapshot.child(item.id).child('top').set(item.top);
            dataSnapshot.child(item.id).child('width').set(item.width);
            dataSnapshot.child(item.id).child('height').set(item.height);
            dataSnapshot.child(item.id).child('fontSize').set(item.fontSize);
            dataSnapshot.child(item.id).child('text').set(item.text);
            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
        }
    }
}
