export const actionToggleDWordDisplay = (item) => {
    return (dispatch, getState) => {
        let editDWordDisplay = {
            id: item.id,
            tag: item.tag,
            divide: item.divide,
            width: item.width,
            top: item.top,
            left: item.left,
            fontSize: item.fontSize,
            text: item.text,
        }
        if (getState().auth.editMode || getState().auth.addMode) {
            dispatch({ type: 'OFF_ALL_STATE' })
            dispatch({ type: 'TOGGLE_EDIT_DWORDDISPLAY' })
            dispatch({ type: 'TOGGLE_DWORDDISPLAY', editDWordDisplay })
        }
    }
}
export const actionAddDWordDisplay = () => {
    return (dispatch, getState) => {
        const dwordDisplay = getState().dwordDisplay.addDWordDisplay
        let tempDWordDisplay = {
            parent: getState().element.parentScada,
            top: parseInt(dwordDisplay.top),
            left: parseInt(dwordDisplay.left),
            text: dwordDisplay.text,
            width: parseInt(dwordDisplay.width),
            fontSize: parseInt(dwordDisplay.fontSize),
            tag: dwordDisplay.tag,
            divide: parseInt(dwordDisplay.divide),
        }
        dataSnapshot.push(tempDWordDisplay);
        dataSnapshotCommand.child('COMPC').set(getState().element.parentScada + '|Refresh_DWordDisplay');
        let message = 'Success';
        let status = 'success';
        dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
    }
}
export const actionDeleteDWordDisplay = () => {
    return (dispatch, getState) => {
        const id = getState().dwordDisplay.editDWordDisplay.id
        if (id !== '') {
            dataSnapshot.child(id).remove();
            dispatch({ type: 'RESET_DWORDDISPLAY_EDIT_FORM' })
            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
        }
    }
}
export const actionUpdateDWordDisplay = () => {
    return (dispatch, getState) => {
        const item = getState().dwordDisplay.editDWordDisplay;
        if (item.id !== '') {
            dataSnapshot.child(item.id).child('tag').set(item.tag);
            dataSnapshot.child(item.id).child('divide').set(item.divide);
            dataSnapshot.child(item.id).child('left').set(item.left);
            dataSnapshot.child(item.id).child('top').set(item.top);
            dataSnapshot.child(item.id).child('width').set(item.width);
            dataSnapshot.child(item.id).child('fontSize').set(item.fontSize);
            dataSnapshot.child(item.id).child('text').set(item.text);
            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
        }
    }
}
