import React, { Component } from 'react'
import Button from '@material-ui/core/Button';

class BitAdjustToggleAdd1 extends Component {
    render() {
    const style={
        position:'absolute',
        left:parseInt(this.props.left),
        top:parseInt(this.props.top),
        padding:'1px',
        color: '#e6ffff',
        backgroundColor:'#3333ff',
        width:parseInt(this.props.width),
        height:parseInt(this.props.height),
        fontSize:parseInt(this.props.fontSize),
        boxShadow: '3px 5px 8px #414751',
        zIndex: 10,

    }
    return (
            <Button variant="outlined" style={style}>
                {this.props.text}
            </Button>
        )
    }
}
export default (BitAdjustToggleAdd1);