import { firebaseConnection } from "../../firebase";
var dataSnapshot = firebaseConnection.database().ref('Table1/Label');
export const pushDataLabel = (newLabel) => {
    return (dispatch) => {
        let temp = {
            text: newLabel.text,
            top: newLabel.top,
            left: newLabel.left
        }
        dataSnapshot.push(temp);
        // dispatch({ type: 'ADD_DATA' })
    }
}
export const actionToggleLabel = (editLabel) => {
    return (dispatch, getState) => {
        let Label = {
            id: editLabel.id,
            text: editLabel.text,
            top: editLabel.top,
            left: editLabel.left,
            color: editLabel.color,
            fontSize: editLabel.fontSize,
            width: editLabel.width,
        }
        if (getState().auth.editMode || getState().auth.addMode) {
            dispatch({ type: 'OFF_ALL_STATE' })
            dispatch({ type: 'TOGGLE_EDIT_LABEL' })
            dispatch({ type: 'TOGGLE_LABEL', Label })
        }
    }
}
export const actionAddLabel = () => {
    return (dispatch, getState) => {
        const label = getState().label.addLabel
        let tempLabel = {
            parent: getState().element.parentScada,
            top: parseInt(label.top),
            left: parseInt(label.left),
            text: label.text,
            color: label.color,
            fontSize: parseInt(label.fontSize),
            width: parseInt(label.width),
        }
        dataSnapshot.push(tempLabel);
        let message = 'Success';
        let status = 'success';
        dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
    }
}
export const actionUpdateLabel = () => {
    return (dispatch, getState) => {
        const editLabel = getState().label.editLabel;
        if (editLabel.id !== '') {
            dataSnapshot.child(editLabel.id).update({
                text: editLabel.text,
                top: parseInt(editLabel.top),
                left: parseInt(editLabel.left),
                color: editLabel.color,
                fontSize: editLabel.fontSize,
                width: editLabel.width,
            })
            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
        }
    }
}
export const actionDeleteLabel = () => {
    return (dispatch, getState) => {
        const editLabel = getState().label.editLabel;
        if (editLabel.id !== '') {
            dataSnapshot.child(editLabel.id).remove();
            dispatch({ type: 'RESET_LABEL_EDIT_FORM' })
            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
        }
    }
}

