import React, { Component } from 'react'
import Button from '@material-ui/core/Button';

class WordAdjustAdd extends Component {
    render() {
    const style={
        position:'absolute',
        left:parseInt(this.props.left),
        top:parseInt(this.props.top),
        padding:'1px',
        color: '#2407ff',
        backgroundColor: '#68b7e8',
        width:parseInt(this.props.width),
        height:parseInt(this.props.height),
        fontSize:parseInt(this.props.fontSize),
        zIndex:10
    }
    return (
            <Button variant="outlined"  style={style}>
                {this.props.text}
            </Button>
        )
    }
}
export default (WordAdjustAdd);