import React, { Component } from 'react';
import Item from './Item'
class List extends Component {
    constructor(props) {
        super(props)
        this.state = {
            dataFirebase: []
        }
    }
    getData = () => {
        if (this.props.dataFirebaseBitDisplay.length > 0) {
            return this.props.dataFirebaseBitDisplay.map((value, key) => {
                return (
                    <Item
                        key={key}
                        i={value.key}
                        tag={value.tag}
                        left={value.left}
                        top={value.top}
                        text0={value.text0}
                        text1={value.text1}
                        color0={value.color0}
                        color1={value.color1}
                        fontSize={value.fontSize}
                        width={value.width}
                        type={value.type}
                        status={value.status}
                        nameImage0={value.nameImage0}
                        nameImage1={value.nameImage1}
                    />
                )
            })
        }
    };
    render() {
        return (
            <div>
                <div id="noteList" role="tablist" aria-multiselectable="true">
                    {this.getData()}
                </div>
            </div>
        );
    }
}

export default (List);