import React, { Component } from 'react'
class ImageAdd extends Component {
    render() {
        const imageStyle = {
            position:'absolute',
            top: parseInt(this.props.top),
            left:parseInt(this.props.left)
        };
        
    return (
            <img alt="image_object" src={this.props.url} style={imageStyle}/>
        )
    }
 
}
export default (ImageAdd)