import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import Button from '@material-ui/core/Button';
import * as actionToggle from '../../../redux/actions/authAction'
import * as action from '../../../redux/actions/textFieldInputAction'
import List from './List'
const utilizeFocus = () => {
	const ref = React.createRef()
	const setFocus = () => {ref.current &&  ref.current.focus()}
	return {setFocus, ref} 
}

class TextFieldFormEdit extends Component {
  constructor(props){
    super(props)
    this.input1FocusTextFieldInputFormEdit = utilizeFocus()
  }
  componentDidMount(){
    this.input1FocusTextFieldInputFormEdit.setFocus()
  }
  componentDidUpdate(prevProps, prevState) {
    // only update chart if the data has changed
    if (prevProps.id !== this.props.id) {
        this.input1FocusTextFieldInputFormEdit.setFocus()
    }
  }
  handleChangeTag = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditTagChange(e.target.value);
  };
  handleChangeLabel = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditLabelChange(e.target.value);
  };
  handleChangeValue = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditValueChange(e.target.value);
  };
  handleChangeLeft = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditLeftChange(e.target.value);
  };
  handleChangeTop = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditTopChange(e.target.value);
  };
  handleChangeWidth = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditWidthChange(e.target.value);
  };
  handleChangeColor = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditColorChange(e.target.value);
  };
  handleChangeFontSize = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditFontSizeChange(e.target.value);
  };
  handleChangeRows = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditRowsChange(e.target.value);
  };
  handleChangeText = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditTextChange(e.target.value);
  };

  handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      this.props.actionUpdateTextField();
    }
  }
  handleKeyPress = (event) => {
    var offsetShift = 0, offsetCtrl = 0, offestDefault = 0;
    if (event.shiftKey) {//shift
      offsetShift = 10; offestDefault = 0;
    } else { offsetShift = 0; }
    if (event.ctrlKey) {//control
      offsetCtrl = 5; offestDefault = 0;
    } else { offsetCtrl = 0; }
    if (!event.shiftKey && !event.ctrlKey) { offestDefault = 1 }
    if (event.keyCode === 37 && (!event.shiftKey || !event.ctrlKey)) {//Trai
      this.props.actionEditMoveLeftDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 38 && (!event.shiftKey || !event.ctrlKey)) {//Len
      this.props.actionEditMoveTopDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 39 && (!event.shiftKey || !event.ctrlKey)) {//Phai
      this.props.actionEditMoveLeftIncrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 40 && (!event.shiftKey || !event.ctrlKey)) {//Xuong
      this.props.actionEditMoveTopIncrease(offsetShift + offsetCtrl + offestDefault)
    }

    if (event.keyCode === 37 && (event.shiftKey && event.ctrlKey)) {//Trai
      this.props.actionEditWidthDecrease()
    }
    if (event.keyCode === 39 && (event.shiftKey && event.ctrlKey)) {//Phai
      this.props.actionEditWidthIncrease()
    }
  }
  render() {
    return (
      <Col style={{ overflow: "auto", height:'960px' }} sm="12" md="4" lg="3">
        <form autoComplete="off" key={this.props.id} onKeyDown={this.handleKeyPress} onKeyUp={this.handleKeyUp}>
          <div style={{ display: 'grid' }}>
            <Button variant="outlined" color="primary" onClick={this.props.actionToggleAddTextFieldInput}>
              CHANGE ADD
            </Button>
            <Button variant="outlined" color="secondary" onClick={this.props.actionToggleOffAll}>
              CLOSE
            </Button>
          </div>
          <hr style={{
            color: '#000000',
            backgroundColor: '#000000',
            height: .5,
            borderColor: '#000000'
          }} />

          <h3 className="text-center text-info">CHANGE TEXT FIELD INPUT PARAMETER</h3>
          <button type="button" onClick=
            {this.props.actionUpdateTextField}
            className="btn btn-primary btn-block">Update</button>

          <div className="dropdown-divider"></div>

          <button type="button" onClick=
            {this.props.actionDeleteTextField}
            className="btn btn-primary btn-block">DELETE</button>
          <div className="form-group">
            <small id="id" className="form-text text-muted">ID</small>
            <input ref={this.input1FocusTextFieldInputFormEdit.ref} value={this.props.id} type="text" className="form-control" name="id" id="id" aria-describedby="helpIdTitle" placeholder="ID: " />

            <small className="form-text text-muted">Tag</small>
            <input value={this.props.tag} onChange={this.handleChangeTag} type="text" className="form-control" name="text" aria-describedby="helpIdTitle" placeholder="Tag: " />

            <small className="form-text text-muted">Label</small>
            <input value={this.props.label} onChange={this.handleChangeLabel} type="text" className="form-control" name="text" aria-describedby="helpIdTitle" placeholder="Label: " />

            <small className="form-text text-muted">Value</small>
            <input value={this.props.value} onChange={this.handleChangeValue} type="text" className="form-control" name="text" aria-describedby="helpIdTitle" placeholder="Value: " />

            <small id="left" className="form-text text-muted">Left</small>
            <input value={this.props.left} onChange={this.handleChangeLeft} type="text" className="form-control" name="left" aria-describedby="helpIdTitle" placeholder="Left: " />

            <small id="top" className="form-text text-muted">Top</small>
            <input value={this.props.top} onChange={this.handleChangeTop} type="text" className="form-control" name="top" aria-describedby="helpIdTitle" placeholder="Top: " />

            <small id="width" className="form-text text-muted">Width</small>
            <input value={this.props.width} onChange={this.handleChangeWidth} type="text" className="form-control" name="width" aria-describedby="helpIdTitle" placeholder="Width: " />

            <small id="top" className="form-text text-muted">Color</small>
            <input type="color" style={{ width: '100%' }} id="color" onChange={this.handleChangeColor} value={this.props.color} />
            <input value={this.props.color} onChange={this.handleChangeColor} type="text" className="form-control" name="color" aria-describedby="helpIdTitle" placeholder="Color: " />

            <small id="top" className="form-text text-muted">FontSize</small>
            <input value={this.props.fontSize} onChange={this.handleChangeFontSize} type="text" className="form-control" name="fontSize" aria-describedby="helpIdTitle" placeholder="FontSize: " />

            <small id="rows" className="form-text text-muted">Rows</small>
            <input value={this.props.rows} onChange={this.handleChangeRows} type="text" className="form-control" name="fontSize" aria-describedby="helpIdTitle" placeholder="Rows: " />

          </div>
          <h7 style={{ color: 'red' }}>Press Shift + Ctrl + Arrow to Change Width and Hight</h7><br />
          <h7 style={{ color: 'red' }}>Press Shift + Arrow to move 10 pixcel </h7><br />
          <h7 style={{ color: 'red' }}>Press Ctrl + Arrow to move 5 pixcel </h7>
        </form>
          <List dataFirebaseTextFieldInput={this.props.dataFirebaseTextFieldInput}/>
      </Col>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    id: state.textFieldInput.editTextField.id,
    tag: state.textFieldInput.editTextField.tag,
    label: state.textFieldInput.editTextField.label,
    value: state.textFieldInput.editTextField.value,
    left: state.textFieldInput.editTextField.left,
    top: state.textFieldInput.editTextField.top,
    width: state.textFieldInput.editTextField.width,
    color: state.textFieldInput.editTextField.color,
    fontSize: state.textFieldInput.editTextField.fontSize,
    rows: state.textFieldInput.editTextField.rows,
  }
}
const mapDispatchToProps = (dispatch) => {
  return {
    actionUpdateTextField: (editItem) => {
      dispatch(action.actionUpdateTextField(editItem))
    },
    actionDeleteTextField: (id) => {
      dispatch(action.actionDeleteTextField(id))
    },
    actionTextFieldEditTagChange: (i) => {
      dispatch(action.actionTextFieldEditTagChange(i))
    },
    actionTextFieldEditLabelChange: (i) => {
      dispatch(action.actionTextFieldEditLabelChange(i))
    },
    actionTextFieldEditValueChange: (i) => {
      dispatch(action.actionTextFieldEditValueChange(i))
    },
    actionTextFieldEditLeftChange: (i) => {
      dispatch(action.actionTextFieldEditLeftChange(i))
    },
    actionTextFieldEditTopChange: (i) => {
      dispatch(action.actionTextFieldEditTopChange(i))
    },
    actionTextFieldEditWidthChange: (i) => {
      dispatch(action.actionTextFieldEditWidthChange(i))
    },
    actionEditWidthDecrease: () => {
      dispatch(action.actionEditWidthDecrease())
    },
    actionEditWidthIncrease: () => {
      dispatch(action.actionEditWidthIncrease())
    },
    actionTextFieldEditColorChange: (i) => {
      dispatch(action.actionTextFieldEditColorChange(i))
    },
    actionTextFieldEditFontSizeChange: (i) => {
      dispatch(action.actionTextFieldEditFontSizeChange(i))
    },
    actionTextFieldEditRowsChange: (i) => {
      dispatch(action.actionTextFieldEditRowsChange(i))
    },
    actionTextFieldEditTextChange: (i) => {
      dispatch(action.actionTextFieldEditTextChange(i))
    },

    actionEditMoveLeftDecrease: (i) => {
      dispatch(action.actionEditMoveLeftDecrease(i))
    },
    actionEditMoveLeftIncrease: (i) => {
      dispatch(action.actionEditMoveLeftIncrease(i))
    },
    actionEditMoveTopDecrease: (i) => {
      dispatch(action.actionEditMoveTopDecrease(i))
    },
    actionEditMoveTopIncrease: (i) => {
      dispatch(action.actionEditMoveTopIncrease(i))
    },

    actionToggleAddTextFieldInput: () => {
      dispatch(actionToggle.actionToggleAddTextFieldInput())
    },
    actionToggleOffAll: () => {
      dispatch(actionToggle.actionToggleOffAll())
    },
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(TextFieldFormEdit);