import { firebaseConnection } from "../../firebase";
var dataSnapshot = firebaseConnection.database().ref('Table1/Image');
export const pushData = (newImage) => {
    return (dispatch) => {
        let temp = {
            src: newImage.src,
            top: newImage.top,
            left: newImage.left
        }
        dataSnapshot.push(temp);
        // dispatch({ type: 'ADD_DATA' })
    }
}
export const actionToggleImage = (ImageChoise) => {
    return (dispatch, getState) => {
        let Image = {
            id: ImageChoise.id,
            src: ImageChoise.src,
            top: ImageChoise.top,
            left: ImageChoise.left,
            nameElement: ImageChoise.nameElement,
            tag: ImageChoise.tag,
        }
        if (getState().auth.editMode || getState().auth.addMode) {
            dispatch({ type: 'OFF_ALL_STATE' })
            dispatch({ type: 'TOGGLE_EDIT_IMAGE' })
            dispatch({ type: 'TOGGLE_IMAGE', Image })
        } else {
            dispatch({ type: 'CLICK_IMAGE', Image })
            dispatch({ type: 'BACKGROUND_IMAGE_CLICK' })
        }
    }
}
export const actionAddImage = () => {
    return (dispatch, getState) => {
        const image = getState().image.addImage
        let Image = {
            parent: getState().element.parentScada,
            top: image.top,
            left: image.left,
            nameElement: image.nameElement,
            src: image.nameImage,
            tag: image.tag,
            state: 0
        }
        dataSnapshot.push(Image);
        let message = 'Success';
        let status = 'success';
        dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
    }
}
export const actionUpdateImage = (editImage) => {
    return (dispatch,getState) => {
        let Image = {
            id: editImage.id,
            src: editImage.src,
            top: editImage.top,
            left: editImage.left,
            tag: editImage.tag,
            nameElement: editImage.nameElement,
            parent: getState().element.parentScada,
        }
        if (editImage.id !== '') {
            dataSnapshot.child(editImage.id).child('tag').set(editImage.tag);
            dataSnapshot.child(editImage.id).child('left').set(editImage.left);
            dataSnapshot.child(editImage.id).child('top').set(editImage.top);
            dataSnapshot.child(editImage.id).child('nameElement').set(editImage.nameElement);
            dispatch({ type: 'UPDATE_IMAGE', Image })
            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
            dispatch({ type: 'RESET_IMAGE_EDIT_FORM' });
        }

    }
}
export const actionDeleteImage = () => {
    return (dispatch, getState) => {
        const editImage = getState().image.editImage;
        if (editImage.id !== '') {
            dataSnapshot.child(editImage.id).remove();
            dispatch({ type: 'RESET_IMAGE_EDIT_FORM' })
            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
        }
    }
}

