import React, { Component } from 'react'
import Switch from "react-switch";

class BitAdjustToggleAdd3 extends Component {
    constructor() {
        super();
        this.state = { checked: false };
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(checked) {
        this.setState({ checked });
    }
    render() {
        const styleBitAdjust = {
            position: 'absolute',
            left: parseInt(this.props.left),
            top: parseInt(this.props.top),
            zIndex: 10,
        }
        return (
            <div style={styleBitAdjust}>
                <Switch
                    height={parseInt(this.props.height)}
                    width={parseInt(this.props.width)}
                    onChange={this.handleChange}
                    checked={true}
                    uncheckedIcon={
                        <div
                            style={{
                                display: "flex",
                                justifyContent: "center",
                                alignItems: "center",
                                height: "100%",
                                fontSize: 15,
                                color: "orange",
                                paddingRight: 2
                            }}
                        >
                            Off
                        </div>
                    }
                    checkedIcon={
                        <div
                            style={{
                                display: "flex",
                                justifyContent: "center",
                                alignItems: "center",
                                height: "100%",
                                fontSize: 15,
                                color: "orange",
                                paddingRight: 2
                            }}
                        >
                            On
                        </div>
                    }
                    className="react-switch"
                    id="icon-switch"
                />
            </div>
        );
    }
}
export default (BitAdjustToggleAdd3);