import React, { Component } from 'react'
import { connect } from 'react-redux';
// import TextField from '@material-ui/core/TextField';
// import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import InputBase from '@material-ui/core/InputBase';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
// import MenuIcon from '@material-ui/icons/Menu';
// import SearchIcon from '@material-ui/icons/Search';
import InputIcon from '@material-ui/icons/Input';
class TextFieldInputAdd extends Component {
    constructor(props) {
        super(props);
        this.state = {
            value: '',
        }
    }
    componentWillMount() {
        this.setState({
            value: this.props.value,
        })
    }
    componentDidUpdate(prevProps, prevState) {
        // only update chart if the data has changed
        if (prevProps.id !== this.props.id) {
            this.setState({
                value: this.props.value,
            })
        }
    }
    onChange = (event) => {
        this.setState({
            'value': event.target.value
        });
      };
    render() {
        return (
            <Paper component="form" 
                style={{
                    position: 'absolute',
                    top: parseInt(this.props.top),
                    left: parseInt(this.props.left),
                    padding: '2px 2px',
                    display: 'flex',
                    alignItems: 'center',
                    width: parseInt(this.props.width),
                    zIndex: 10,
                }}>
                <InputBase
                    style={{
                        marginLeft: 1,
                        flex: 1,
                        color: this.props.color,
                        fontSize: parseInt(this.props.fontSize),
                    }}
                    placeholder={this.props.label}
                    value={this.state.value}
                    onChange={this.onChange}
                />
                <Divider style={{height: 28,margin: 4,}} orientation="vertical" />
                <IconButton color="primary" style={{padding: 10,}} aria-label="directions">
                    <InputIcon />
                </IconButton>
            </Paper>
        )
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
    }
}
export default connect(null, mapDispatchToProps)(TextFieldInputAdd)