import React, { Component } from 'react'
import { connect } from 'react-redux';
import Button from '@material-ui/core/Button';
import {actionToggleWordDisplay} from '../../../redux/actions/wordDisplayAction'

class WordDisplay extends Component {
    handleWordDisplayClick=()=>{
        this.props.actionToggleWordDisplay({
            id:this.props.i,
            tag:this.props.tag,
            width:this.props.width,
            left:this.props.left,
            top:this.props.top,
            fontSize:this.props.fontSize,
            text:this.props.text,
            divide:typeof(this.props.divide)==='undefined'?1:this.props.divide,
        })
    }
    render() {
    const style={
        position:'absolute',
        left:parseInt(this.props.left),
        top:parseInt(this.props.top),
        padding:'1px',
        color: '#80ffff',
        width:parseInt(this.props.width),
        fontSize:parseInt(this.props.fontSize),
        zIndex:10
    }
    return (
            <Button onClick={this.handleWordDisplayClick} variant="outlined"  style={style}>
                {(typeof(this.props.divide)!=='undefined' && parseInt(this.props.divide)!==0)?parseFloat(this.props.text)/parseInt(this.props.divide):this.props.text}
            </Button>
        )
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        actionToggleWordDisplay: (item) => {
        dispatch(actionToggleWordDisplay(item))
      },
    }
}
export default connect(null,mapDispatchToProps)(WordDisplay);