import { firebaseConnection } from "../../firebase";
var dataSnapshot = firebaseConnection.database().ref('Table1/TextFieldInput');
var dataSnapshotCommand = firebaseConnection.database().ref('Table1/MQTT/SUBSCRIBE_TO_PLC');
export const pushDataTextField = (newTextField) => {
    return (dispatch) => {
        let temp = {
            label: newTextField.label,
            value: newTextField.value,
            left: newTextField.left,
            top: newTextField.top,
            width: newTextField.width,
            color: newTextField.color,
            fontSize: newTextField.fontSize,
            rows: newTextField.rows,
            text: newTextField.text,
        }
        dataSnapshot.push(temp);
        // dispatch({ type: 'ADD_DATA' })
    }
}
export const actionToggleTextField = (editTextField) => {
    return (dispatch, getState) => {
        let TextField = {
            id: editTextField.id,
            tag: editTextField.tag,
            label: editTextField.label,
            value: editTextField.value,
            left: editTextField.left,
            top: editTextField.top,
            width: editTextField.width,
            color: editTextField.color,
            fontSize: editTextField.fontSize,
            rows: editTextField.rows,
            text: editTextField.text,
        }
        if (getState().auth.editMode || getState().auth.addMode) {
            dispatch({ type: 'OFF_ALL_STATE' })
            dispatch({ type: 'TOGGLE_EDIT_TEXT_FIELD_INPUT' })
            dispatch({ type: 'TOGGLE_TEXT_FIELD_INPUT', TextField })
        }else {
            if (TextField.id !== '') {
                if (!isNaN(parseInt(TextField.tag))) {
                    dataSnapshotCommand.child('COMPC').set(getState().element.parentScada + '|BitAdjust|' + TextField.tag.toString()+'|'+TextField.value);
                }
            }
        }
    }
}
export const actionAddTextField = () => {
    return (dispatch, getState) => {
        const textField = getState().textFieldInput.addTextField
        let tempTextField = {
            parent: getState().element.parentScada,
            tag: parseInt(textField.tag),
            top: parseInt(textField.top),
            left: parseInt(textField.left),
            width: parseInt(textField.width),
            fontSize: parseInt(textField.fontSize),
            rows: parseInt(textField.rows),
            value: textField.value,
            label: textField.label,
            color: textField.color,
            text: textField.text,
        }
        dataSnapshot.push(tempTextField);
        let message = 'Success';
        let status = 'success';
        dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
    }
}
export const actionUpdateTextField = () => {
    return (dispatch, getState) => {
        const editTextField = getState().textFieldInput.editTextField;
        if (editTextField.id !== '') {
            dataSnapshot.child(editTextField.id).update({
                tag: parseInt(editTextField.tag),
                top: parseInt(editTextField.top),
                left: parseInt(editTextField.left),
                width: parseInt(editTextField.width),
                fontSize: parseInt(editTextField.fontSize),
                rows: parseInt(editTextField.rows),
                value: editTextField.value,
                label: editTextField.label,
                color: editTextField.color,
            })
            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
        }
    }
}
export const actionDeleteTextField = () => {
    return (dispatch, getState) => {
        const editTextField = getState().textFieldInput.editTextField;
        if (editTextField.id !== '') {
            dataSnapshot.child(editTextField.id).remove();
            dispatch({ type: 'RESET_TEXT_FIELD_INPUT_EDIT_FORM' })
            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
        }
    }
}

