import React, { Component } from 'react'
import { connect } from 'react-redux';
import * as action from '../../redux/actions/dwordAdjustAction'
class InputDWordAdjustClick extends Component {
    constructor(props) {
        super(props);
        this.state = {
            text: '',
        }
    }
    isChange = (evt) => {
        const name = evt.target.name;
        const value = evt.target.value;
        this.setState({
            [name]: value
        });
    };
    componentWillMount(){
        this.setState({
            text: this.props.text,
        })
    }
    componentDidUpdate(prevProps, prevState) {
        // only update chart if the data has changed
        if (prevProps.id !== this.props.id) {
            this.setState({
                text: this.props.text,
            })
        }
    }
    handleKeyUp = (event) => {
        if (event.keyCode === 13) {
            this.props.actionDWordAdjustEnter({
                id:this.props.id,
                text:this.state.text,
                tag:this.props.tag
            })
        }
        if (event.keyCode === 27) {
            this.props.actionDWordAdjustCancel()
        }
    }
    render() {
        const DWordAdjustClickStyle = {
            position: 'absolute',
            top: parseInt(this.props.top),
            left: parseInt(this.props.left),
            width: parseInt(this.props.width),
            height:parseInt(this.props.height),
            fontSize: parseInt(this.props.fontSize),
            backgroundColor: 'yellow',
            textAlign: 'center',
            zIndex: 11,
    };
        return (
            <input onClick={this.props.actionInputDWordAdjustClick} onKeyDown={this.handleKeyUp} style={DWordAdjustClickStyle} value={this.state.text} onChange={(evt) => { this.isChange(evt) }} type="text" className="form-control" name="text" id="text" aria-describedby="helpIdTitle" />
        )
    }
}
const mapStateToProps = (state, ownProps) => {
    return {
        id: state.dwordAdjust.clickDWordAdjust.id,
        tag: state.dwordAdjust.clickDWordAdjust.tag,
        left: state.dwordAdjust.clickDWordAdjust.left,
        top: state.dwordAdjust.clickDWordAdjust.top,
        text: state.dwordAdjust.clickDWordAdjust.text,
        width: state.dwordAdjust.clickDWordAdjust.width,
        height: state.dwordAdjust.clickDWordAdjust.height,
        fontSize: state.dwordAdjust.clickDWordAdjust.fontSize,
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        actionInputDWordAdjustClick: () => {
            dispatch(action.actionInputDWordAdjustClick())
        },
        actionDWordAdjustEnter: (item) => {
            dispatch(action.actionDWordAdjustEnter(item))
        },
        actionDWordAdjustCancel: () => {
            dispatch(action.actionDWordAdjustCancel())
        },
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(InputDWordAdjustClick)