import React, { Component } from 'react'

class BitDisplayAdd1 extends Component {
    render() {
    const style={
        position:'absolute',
        left:parseInt(this.props.left),
        top:parseInt(this.props.top),
        padding:'1px',
        color: this.props.color0,
        width:parseInt(this.props.width),
        fontSize:parseInt(this.props.fontSize),
        fontWeight:'bold',
        zIndex: 10,
        textAlign:'center'
    }
    return (
            <div style={style}>
                {this.props.text0}
            </div>
        )
    }
}
export default (BitDisplayAdd1);