import React, { Component } from 'react'

class BitDisplayAdd2 extends Component {
    render() {
    const style={
        position:'absolute',
        left:parseInt(this.props.left),
        top:parseInt(this.props.top),
        width:parseInt(this.props.width),
        zIndex: 10,
    }
    let src;
    if(this.props.url0){
        src=this.props.url0?this.props.url0:'';
    }
    if(this.props.url1){
        src=this.props.url1?this.props.url1:'';
    }
    return (
        <img alt="image_object" src={src} style={style}/>
        )
    }
}
export default (BitDisplayAdd2);