import React, { Component } from 'react'
import { connect } from 'react-redux';
import Button from '@material-ui/core/Button';
import {actionToggleBitAdjust} from '../../../redux/actions/bitAdjustAction'

class BitAdjustToggle1 extends Component {
    handleBitAdjustClick=()=>{
        this.props.actionToggleBitAdjust({
            id:this.props.i,
            tag:this.props.tag,
            tagTextFieldInput:this.props.tagTextFieldInput,
            width:this.props.width,
            height:this.props.height,
            left:this.props.left,
            top:this.props.top,
            fontSize:this.props.fontSize,
            text:this.props.text,
            type:this.props.type,
            status:this.props.status,
        })
    }
    render() {
    const getColor=(this.props.status?'	rgb(0, 255, 0)':'#3333ff')
    const style={
        position:'absolute',
        left:parseInt(this.props.left),
        top:parseInt(this.props.top),
        padding:'1px',
        color: '#e6ffff',
        backgroundColor:getColor,
        width:parseInt(this.props.width),
        height:parseInt(this.props.height),
        fontSize:parseInt(this.props.fontSize),
        boxShadow: '3px 5px 8px #414751',
        zIndex: 10,
    }
    return (
            <Button onClick={this.handleBitAdjustClick} style={style}>
                {this.props.text?this.props.text:''}
            </Button>
        )
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        actionToggleBitAdjust: (item) => {
        dispatch(actionToggleBitAdjust(item))
      },
    }
}
export default connect(null,mapDispatchToProps)(BitAdjustToggle1);