import React, { Component } from 'react'
import { connect } from 'react-redux'
import { actionToggleBitDisplay } from '../../../redux/actions/bitDisplayAction'
import { Button } from 'reactstrap';
class LabelItem extends Component {
    handleClick=()=>{
        this.props.actionToggleBitDisplay({
            id:this.props.i,
            tag:this.props.tag,
            width:this.props.width,
            left:this.props.left,
            top:this.props.top,
            fontSize:this.props.fontSize,
            text1:this.props.text1,
            text0:this.props.text0,
            color0:this.props.color0,
            color1:this.props.color1,
            type:this.props.type,
            nameImage0:this.props.nameImage0,
            nameImage1:this.props.nameImage1,
        })
    }
    render() {
        const { i,tag, text0,text1,left,top } = this.props;
        return (
            <div>
                <div className="card">
                <Button color="primary" className="px-4" onClick={this.handleClick}>Choose</Button>
                    <div className="card-header"  id="note1">
                        <h6 className="mb-0">
                            <a data-toggle="collapse" data-parent="#noteList" href={"#noteContent" + i} aria-expanded="true" aria-controls="noteContent1">
                                tag: {tag} - text0: {text0}
                            </a>
                        </h6>
                    </div>
                    <div id={"noteContent" + i} className="collapse in" role="tabpanel" aria-labelledby="note1">
                        <div className="card-body">
                            Left:{left} - Top:{top} - text1: {text1}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
const mapStateToProps = (state, ownProps) => {
    return {
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        actionToggleBitDisplay: (i) => {
            dispatch(actionToggleBitDisplay(i))
        },
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(LabelItem);