import React, { Component } from 'react'
import { connect } from 'react-redux';
import Button from '@material-ui/core/Button';
import {actionToggleDWordDisplay} from '../../../redux/actions/dwordDisplayAction'

class DWordDisplay extends Component {
    handleDWordDisplayClick=()=>{
        this.props.actionToggleDWordDisplay({
            id:this.props.i,
            tag:this.props.tag,
            width:this.props.width,
            left:this.props.left,
            top:this.props.top,
            fontSize:this.props.fontSize,
            text:this.props.text,
            divide:typeof(this.props.divide)==='undefined'?1:this.props.divide,
            zIndex:10
        })
    }
    render() {
    const style={
        position:'absolute',
        left:parseInt(this.props.left),
        top:parseInt(this.props.top),
        padding:'1px',
        color: '#0040ff',
        backgroundColor:'#ccf2ff',
        width:parseInt(this.props.width),
        fontSize:parseInt(this.props.fontSize),
        zIndex:100,
    }
    return (
            <Button onClick={this.handleDWordDisplayClick} variant="outlined"  style={style}>
                {(typeof(this.props.divide)!=='undefined' && parseInt(this.props.divide)!==0)?parseFloat(this.props.text)/parseInt(this.props.divide):this.props.text}
            </Button>
        )
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        actionToggleDWordDisplay: (item) => {
        dispatch(actionToggleDWordDisplay(item))
      },
    }
}
export default connect(null,mapDispatchToProps)(DWordDisplay);