export const actionToggleBitDisplay = (item) => {
    return (dispatch, getState) => {
        let bitDisplay = {
            id: item.id,
            tag: item.tag,
            width: item.width,
            top: item.top,
            left: item.left,
            fontSize: item.fontSize,
            text0: item.text0,
            text1: item.text1,
            color0: item.color0,
            color1: item.color1,
            nameImage0: item.nameImage0,
            nameImage1: item.nameImage1,
            status: item.status,
            type: item.type,
            blink0: item.blink0,
            blink1: item.blink1,
        }
        if (getState().auth.editMode || getState().auth.addMode) {
            dispatch({ type: 'OFF_ALL_STATE' })
            dispatch({ type: 'TOGGLE_EDIT_BITDISPLAY' })
            dispatch({ type: 'TOGGLE_BITDISPLAY', bitDisplay })
        }
    }
}
export const actionAddBitDisplay = () => {
    return (dispatch, getState) => {
        const bitDisplay = getState().bitDisplay.addBitDisplay
        let tempBitDisplay = {
            parent: getState().element.parentScada,
            top: parseInt(bitDisplay.top),
            left: parseInt(bitDisplay.left),
            text0: bitDisplay.text0,
            text1: bitDisplay.text1,
            color0: bitDisplay.color0,
            color1: bitDisplay.color1,
            nameImage0: bitDisplay.nameImage0,
            nameImage1: bitDisplay.nameImage1,
            status: bitDisplay.status,
            width: parseInt(bitDisplay.width),
            fontSize: parseInt(bitDisplay.fontSize),
            tag: bitDisplay.tag,
            type: parseInt(bitDisplay.type),
            blink0: parseInt(bitDisplay.blink0),
            blink1: parseInt(bitDisplay.blink1),
        }
        dataSnapshot.push(tempBitDisplay);
        dataSnapshotCommand.child('COMPC').set(getState().element.parentScada+'|Refresh_BitDisplay');

        let message = 'Success';
        let status = 'success';
        dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
    }
}
export const actionDeleteBitDisplay = () => {
    return (dispatch, getState) => {
        const id = getState().bitDisplay.editBitDisplay.id
        if (id !== '') {
            dataSnapshot.child(id).remove();
            dispatch({ type: 'RESET_BITDISPLAY_EDIT_FORM' })
            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
        }
    }
}
export const actionUpdateBitDisplay = () => {
    return (dispatch, getState) => {
        const item = getState().bitDisplay.editBitDisplay
        if (item.id !== '') {
            dataSnapshot.child(item.id).child('tag').set(item.tag);
            dataSnapshot.child(item.id).child('left').set(item.left);
            dataSnapshot.child(item.id).child('top').set(item.top);
            dataSnapshot.child(item.id).child('width').set(item.width);
            dataSnapshot.child(item.id).child('fontSize').set(item.fontSize);
            dataSnapshot.child(item.id).child('text0').set(item.text0);
            dataSnapshot.child(item.id).child('text1').set(item.text1);
            dataSnapshot.child(item.id).child('color0').set(item.color0);
            dataSnapshot.child(item.id).child('color1').set(item.color1);
            // dataSnapshot.child(item.id).child('nameImage0').set(item.nameImage0);
            // dataSnapshot.child(item.id).child('nameImage1').set(item.nameImage1);
            dataSnapshot.child(item.id).child('type').set(item.type);
            dataSnapshot.child(item.id).child('blink0').set(item.blink0);
            dataSnapshot.child(item.id).child('blink1').set(item.blink1);
            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
        }
    }
}
