import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import arrImageBitDisplay from '../ArrayImageBitDisplay'
import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from "shards-react";
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Button from '@material-ui/core/Button';
import * as actionToggle from '../../../redux/actions/authAction'
import * as action from '../../../redux/actions/bitDisplayAction'
class BitDisplayFormAdd extends Component {
  constructor(props) {
    super(props);
    this.toggle0 = this.toggle0.bind(this);
    this.toggle1 = this.toggle1.bind(this);
    this.state = { open0: false, choise0: '', open1: false, choise1: '' };
  }
  toggle0() {
    this.setState(prevState => {
      return { open0: !prevState.open0 };
    });
  }
  toggle1() {
    this.setState(prevState => {
      return { open1: !prevState.open1 };
    });
  }
  choise0(e) {
    this.setState({ choise0: e })
  }
  choise1(e) {
    this.setState({ choise1: e })
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.actionAddBitDisplay();
  };
  handleChangeText0 = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayAddText0Change(e.target.value);
  };
  handleChangeColor0 = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayAddColor0Change(e.target.value);
  };
  handleChangeText1 = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayAddText1Change(e.target.value);
  };
  handleChangeColor1 = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayAddColor1Change(e.target.value);
  };
  handleChangeWidth = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayAddWidthChange(e.target.value);
  };
  handleChangeFontSize = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayAddFontSizeChange(e.target.value);
  };
  handleChangeTag = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayAddTagChange(e.target.value);
  };
  handleChangeLeft = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayAddLeftChange(e.target.value);
  };
  handleChangeTop = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayAddTopChange(e.target.value);
  };
  handleChangeType = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayAddTypeChange(e.target.value);
  };
  handleChangeBlink0 = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayAddBlink0Change(e.target.value);
  };
  handleChangeBlink1 = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayAddBlink1Change(e.target.value);
  };
  // handleChangeNameImage0 = (e) => {
  //   e.preventDefault();
  //   this.props.actionBitDisplayAddNameImage0Change(e.target.value);
  // };
  // handleChangeNameImage1 = (e) => {
  //   e.preventDefault();
  //   this.props.actionBitDisplayAddNameImage1Change(e.target.value);
  // };
  handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      this.props.actionAddBitDisplay();
    }
  }
  handleKeyPress = (event) => {
    var offsetShift = 0, offsetCtrl = 0, offestDefault = 0;
    if (event.shiftKey) {//shift
      offsetShift = 10; offestDefault = 0;
    } else { offsetShift = 0; }
    if (event.ctrlKey) {//control
      offsetCtrl = 5; offestDefault = 0;
    } else { offsetCtrl = 0; }
    if (!event.shiftKey && !event.ctrlKey) { offestDefault = 1 }
    if (event.keyCode === 37 && (!event.shiftKey || !event.ctrlKey)) {//Trai
      this.props.actionMoveLeftDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 38 && (!event.shiftKey || !event.ctrlKey)) {//Len
      this.props.actionMoveTopDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 39 && (!event.shiftKey || !event.ctrlKey)) {//Phai
      this.props.actionMoveLeftIncrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 40 && (!event.shiftKey || !event.ctrlKey)) {//Xuong
      this.props.actionMoveTopIncrease(offsetShift + offsetCtrl + offestDefault)
    }

    if (event.keyCode === 37 && (event.shiftKey && event.ctrlKey)) {//Trai
      this.props.actionWidthDecrease()
    }
    if (event.keyCode === 39 && (event.shiftKey && event.ctrlKey)) {//Phai
      this.props.actionWidthIncrease()
    }
  }
  GetDropDownItem0 = () => {
    return (arrImageBitDisplay.map((image, key) => {
      return (<DropdownItem key={key} onClick={() => this.props.actionBitDisplayAddNameImage0Change(image.name)}>{image.name}</DropdownItem>)
    }))
  }
  GetDropDownItem1 = () => {
    return (arrImageBitDisplay.map((image, key) => {
      return (<DropdownItem key={key} onClick={() => this.props.actionBitDisplayAddNameImage1Change(image.name)}>{image.name}</DropdownItem>)
    }))
  }
  render() {
    return (
      <Col sm="12" md="4" lg="3">
        <form autoComplete="off" key='AddLineHorizontal' id="login-form" className="form" onSubmit={this.handleSubmit} onKeyDown={this.handleKeyPress} style={{ height: '100vh', overflow: 'auto' }}>
          <div style={{ display: 'grid' }}>
            <Button variant="outlined" color="primary" onClick={this.props.actionToggleEditBitDisplay}>
              CHANGE EDIT
            </Button>
            <Button variant="outlined" color="secondary" onClick={this.props.actionToggleOffAll}>
              CLOSE
            </Button>
          </div>
          <hr style={{
            color: '#000000',
            backgroundColor: '#000000',
            height: .5,
            borderColor: '#000000'
          }} />

          <h3 className="text-center text-info">ADD BITDISPLAY</h3>
          <div className="form-group" style={{ width: '100%' }}>
            <input style={{ width: '100%' }} type="submit" name="submit" className="btn btn-info btn-md" value="Add Data" />
          </div>
          {/* choise type by radio button */}
          <RadioGroup aria-label="position" name="position" value={this.props.BitDisplayType} onChange={this.handleChangeType} row>
            <FormControlLabel
              value={'1'}
              control={<Radio color="primary" />}
              label="Type1"
              labelPlacement="start"
            />
            <FormControlLabel
              value={'2'}
              control={<Radio color="primary" />}
              label="Type2"
              labelPlacement="start"
            />
          </RadioGroup>
          <Dropdown open={this.state.open0} toggle={this.toggle0} className="d-table" size="sm">
            <DropdownToggle>Choise Name Image0</DropdownToggle>
            <DropdownMenu left>
              {this.GetDropDownItem0()}
            </DropdownMenu>
          </Dropdown>
          <div className="form-group">
            <label className="text-info">Name Image0:</label><br />
            <input type="text" placeholder="Name Image0 *" id="nameImage0" className="form-control" value={this.props.BitDisplayNameImage0} />
          </div>
          <Dropdown open={this.state.open1} toggle={this.toggle1} className="d-table" size="sm">
            <DropdownToggle>Choise Name Image1</DropdownToggle>
            <DropdownMenu left>
              {this.GetDropDownItem1()}
            </DropdownMenu>
          </Dropdown>
          <div className="form-group">
            <small className="form-text text-muted">Name Image1:</small>
            <input type="text" placeholder="Name Image1 *" id="nameImage1" className="form-control" value={this.props.BitDisplayNameImage1} />

            <small className="form-text text-muted">Width:</small>
            <input type="text" onChange={this.handleChangeWidth} placeholder="Width *" id="width" className="form-control" value={this.props.BitDisplayWidth} />

            <small className="form-text text-muted">Tag:</small>
            <input type="text" onChange={this.handleChangeTag} placeholder="Tag *" id="tag" className="form-control" value={this.props.BitDisplayTag} />

            <small className="form-text text-muted">Text0:</small>
            <input type="text" onChange={this.handleChangeText0} placeholder="Text0 *" id="text0" className="form-control" value={this.props.BitDisplayText0} />

            <small className="form-text text-muted">Color0:</small>
            <input type="color" style={{ width: '100%' }} id="color0" onChange={this.handleChangeColor0} value={this.props.BitDisplayColor0} />
            <input type="text" onChange={this.handleChangeColor0} placeholder="Color0 *" id="color0" className="form-control" value={this.props.BitDisplayColor0} />

            <small className="form-text text-muted">Text1:</small>
            <input type="text" onChange={this.handleChangeText1} placeholder="Text1 *" id="text1" className="form-control" value={this.props.BitDisplayText1} />

            <small className="form-text text-muted">Color1:</small>
            <input type="color" style={{ width: '100%' }} id="color1" onChange={this.handleChangeColor1} value={this.props.BitDisplayColor1} />
            <input type="text" onChange={this.handleChangeColor1} placeholder="Color1 *" id="color1" className="form-control" value={this.props.BitDisplayColor1} />

            <small className="form-text text-muted">Left:</small>
            <input type="text" onChange={this.handleChangeLeft} placeholder="Left *" id="left" className="form-control" value={this.props.BitDisplayLeft} />

            <small className="form-text text-muted">Top:</small>
            <input type="text" onChange={this.handleChangeTop} placeholder="Top *" id="top" className="form-control" value={this.props.BitDisplayTop} />

            <small className="form-text text-muted">FontSize:</small>
            <input type="text" onChange={this.handleChangeFontSize} placeholder="Font Size *" id="fontSize" className="form-control" value={this.props.BitDisplayFontSize} />
          </div>
          <RadioGroup aria-label="divide" name="divide" value={this.props.blink0} onChange={this.handleChangeBlink0} row>
            <FormControlLabel
              value={0}
              control={<Radio color="primary" />}
              label="Disnable Blink 0"
              labelPlacement="start"
            />
            <FormControlLabel
              value={1}
              control={<Radio color="primary" />}
              label="Enable Blink 0"
              labelPlacement="start"
            />
          </RadioGroup>
          <RadioGroup aria-label="divide" name="divide" value={this.props.blink1} onChange={this.handleChangeBlink1} row>
            <FormControlLabel
              value={0}
              control={<Radio color="primary" />}
              label="Disnable Blink 1"
              labelPlacement="start"
            />
            <FormControlLabel
              value={1}
              control={<Radio color="primary" />}
              label="Enable Blink 1"
              labelPlacement="start"
            />
          </RadioGroup>

          <h7 style={{ color: 'red' }}>Press Shift + Ctrl + Arrow to Change Width and Hight</h7><br />
          <h7 style={{ color: 'red' }}>Press Shift + Arrow to move 10 pixcel </h7><br />
          <h7 style={{ color: 'red' }}>Press Ctrl + Arrow to move 5 pixcel </h7>
        </form>

      </Col>
    )
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
    BitDisplayLeft: state.bitDisplay.addBitDisplay.left,
    BitDisplayTop: state.bitDisplay.addBitDisplay.top,
    BitDisplayFontSize: state.bitDisplay.addBitDisplay.fontSize,
    BitDisplayWidth: state.bitDisplay.addBitDisplay.width,
    BitDisplayTag: state.bitDisplay.addBitDisplay.tag,
    BitDisplayText0: state.bitDisplay.addBitDisplay.text0,
    BitDisplayText1: state.bitDisplay.addBitDisplay.text1,
    BitDisplayColor0: state.bitDisplay.addBitDisplay.color0,
    BitDisplayColor1: state.bitDisplay.addBitDisplay.color1,
    BitDisplayType: state.bitDisplay.addBitDisplay.type,
    BitDisplayNameImage0: state.bitDisplay.addBitDisplay.nameImage0,
    BitDisplayNameImage1: state.bitDisplay.addBitDisplay.nameImage1,
    blink0: parseInt(state.bitDisplay.addBitDisplay.blink0),
    blink1: parseInt(state.bitDisplay.addBitDisplay.blink1),
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionAddBitDisplay: () => {
      dispatch(action.actionAddBitDisplay())
    },
    actionBitDisplayAddText0Change: (item) => {
      dispatch(action.actionBitDisplayAddText0Change(item))
    },
    actionBitDisplayAddText1Change: (item) => {
      dispatch(action.actionBitDisplayAddText1Change(item))
    },
    actionBitDisplayAddColor0Change: (item) => {
      dispatch(action.actionBitDisplayAddColor0Change(item))
    },
    actionBitDisplayAddColor1Change: (item) => {
      dispatch(action.actionBitDisplayAddColor1Change(item))
    },
    actionBitDisplayAddTagChange: (item) => {
      dispatch(action.actionBitDisplayAddTagChange(item))
    },
    actionBitDisplayAddLeftChange: (item) => {
      dispatch(action.actionBitDisplayAddLeftChange(item))
    },
    actionBitDisplayAddTopChange: (item) => {
      dispatch(action.actionBitDisplayAddTopChange(item))
    },
    actionBitDisplayAddFontSizeChange: (item) => {
      dispatch(action.actionBitDisplayAddFontSizeChange(item))
    },
    actionBitDisplayAddWidthChange: (item) => {
      dispatch(action.actionBitDisplayAddWidthChange(item))
    },
    actionWidthDecrease: () => {
      dispatch(action.actionWidthDecrease())
    },
    actionWidthIncrease: () => {
      dispatch(action.actionWidthIncrease())
    },
    actionBitDisplayAddTypeChange: (item) => {
      dispatch(action.actionBitDisplayAddTypeChange(item))
    },
    actionBitDisplayAddNameImage0Change: (item) => {
      dispatch(action.actionBitDisplayAddNameImage0Change(item))
    },
    actionBitDisplayAddNameImage1Change: (item) => {
      dispatch(action.actionBitDisplayAddNameImage1Change(item))
    },
    actionBitDisplayAddBlink0Change: (item) => {
      dispatch(action.actionBitDisplayAddBlink0Change(item))
    },
    actionBitDisplayAddBlink1Change: (item) => {
      dispatch(action.actionBitDisplayAddBlink1Change(item))
    },

    actionMoveLeftDecrease: (i) => {
      dispatch(action.actionMoveLeftDecrease(i))
    },
    actionMoveLeftIncrease: (i) => {
      dispatch(action.actionMoveLeftIncrease(i))
    },
    actionMoveTopDecrease: (i) => {
      dispatch(action.actionMoveTopDecrease(i))
    },
    actionMoveTopIncrease: (i) => {
      dispatch(action.actionMoveTopIncrease(i))
    },

    actionToggleEditBitDisplay: () => {
      dispatch(actionToggle.actionToggleEditBitDisplay())
    },
    actionToggleOffAll: () => {
      dispatch(actionToggle.actionToggleOffAll())
    },
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(BitDisplayFormAdd)