export const actionInputDWordAdjustClick = () => {
    return (dispatch, getState) => {
        dispatch({ type: 'BACKGROUND_DWORDADJUST_CLICK' })
    }
}
export const actionDWordAdjustCancel = () => {
    return (dispatch, getState) => {
        dispatch({ type: 'BACKGROUND_CLICK' })
    }
}
export const actionToggleDWordAdjust = (item) => {
    return (dispatch, getState) => {
        let dwordAdjust = {
            id: item.id,
            tag: item.tag,
            width: item.width,
            height: item.height,
            top: item.top,
            left: item.left,
            fontSize: item.fontSize,
            text: item.text,
        }
        if (getState().auth.editMode || getState().auth.addMode) {
            dispatch({ type: 'OFF_ALL_STATE' })
            dispatch({ type: 'TOGGLE_EDIT_DWORDADJUST' })
            dispatch({ type: 'TOGGLE_DWORDADJUST', dwordAdjust })
        } else {
            dispatch({ type: 'CLICK_DWORDADJUST', dwordAdjust })
            dispatch({ type: 'BACKGROUND_DWORDADJUST_CLICK' })
        }
    }
}
export const actionDWordAdjustEnter = (item) => {
    return (dispatch, getState) => {
        if (item.id !== '') {
            dataSnapshotCommand.child('COMPC').set(getState().element.parentScada + '|DWordAdjust|'+item.tag.toString() + '|' + item.text);
        }
        dispatch({ type: 'BACKGROUND_CLICK' })
    }
}
export const actionAddDWordAdjust = () => {
    return (dispatch, getState) => {
        const dwordAdjust = getState().dwordAdjust.addDWordAdjust
        let tempDWordAdjust = {
            parent: getState().element.parentScada,
            top: parseInt(dwordAdjust.top),
            left: parseInt(dwordAdjust.left),
            text: dwordAdjust.text,
            width: parseInt(dwordAdjust.width),
            height: parseInt(dwordAdjust.height),
            fontSize: parseInt(dwordAdjust.fontSize),
            tag: dwordAdjust.tag,
        }
        dataSnapshot.push(tempDWordAdjust);
        dataSnapshotCommand.child('COMPC').set(getState().element.parentScada+'|Refresh_DWordAdjust');

        let message = 'Success';
        let status = 'success';
        dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
    }
}
export const actionDeleteDWordAdjust = () => {
    return (dispatch, getState) => {
        const id = getState().dwordAdjust.editDWordAdjust.id
        dataSnapshot.child(id).remove();
        dispatch({ type: 'RESET_DWORDADJUST_EDIT_FORM' })
        let message = 'Success';
        let status = 'success';
        dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
    }
}
export const actionUpdateDWordAdjust = () => {
    return (dispatch, getState) => {
        const item = getState().dwordAdjust.editDWordAdjust
        if (item.id !== '') {
            dataSnapshot.child(item.id).child('tag').set(item.tag);
            dataSnapshot.child(item.id).child('left').set(item.left);
            dataSnapshot.child(item.id).child('top').set(item.top);
            dataSnapshot.child(item.id).child('width').set(item.width);
            dataSnapshot.child(item.id).child('height').set(item.height);
            dataSnapshot.child(item.id).child('fontSize').set(item.fontSize);
            dataSnapshot.child(item.id).child('text').set(item.text);
            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
        }
    }
}
