import React, { Component } from 'react'
import { connect } from 'react-redux'
import { actionToggleLabel } from '../../../redux/actions/labelAction'
import { Button } from 'reactstrap';
class LabelItem extends Component {
    handleLabelClick=()=>{
        this.props.actionToggleLabel({
            id:this.props.i,
            text:this.props.text,
            left:this.props.left,
            top:this.props.top,
            color:this.props.color,
            fontSize:this.props.fontSize,
            width:this.props.width,
        })
    }
    render() {
        const { i, text, left, top } = this.props;
        return (
            <div>
                <div className="card">
                <Button color="primary" className="px-4" onClick={this.handleLabelClick}>Choose</Button>
                    <div className="card-header"  id="note1">
                        <h6 className="mb-0">
                            <a data-toggle="collapse" data-parent="#noteList" href={"#noteContent" + i} aria-expanded="true" aria-controls="noteContent1">
                                {text} - {left} - {top}
                            </a>
                        </h6>
                    </div>
                    <div id={"noteContent" + i} className="collapse in" role="tabpanel" aria-labelledby="note1">
                        <div className="card-body">
                            Left:{left} - Top:{top}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
const mapStateToProps = (state, ownProps) => {
    return {
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        actionToggleLabel: (i) => {
            dispatch(actionToggleLabel(i))
        },
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(LabelItem);