import React, { Component } from 'react';
import Item from './Item'
class List extends Component {
    constructor(props) {
        super(props)
        this.state = {
            dataFirebase: []
        }
    }
    getData = () => {
        if (this.props.dataFirebasePanel.length > 0) {
            return this.props.dataFirebasePanel.map((value, key) => {
                return (
                    <Item
                        key={key}
                        i={value.key}
                        width={value.width}
                        height={value.height}
                        left={value.left}
                        top={value.top}
                        margin={value.margin}
                        backgroundColor={value.backgroundColor}
                        padding={value.padding}
                        borderRadius={value.borderRadius}
                        boxShadow1={value.boxShadow1}
                        boxShadow2={value.boxShadow2}
                        boxShadow3={value.boxShadow3}
                        boxShadowColor={value.boxShadowColor}
                        zIndex={value.zIndex}
                    />
                )
            })
        }
    };
    render() {
        return (
            <div>
                <div id="noteList" role="tablist" aria-multiselectable="true">
                    {this.getData()}
                </div>
            </div>
        );
    }
}

export default (List);