import React, { Component } from 'react'
import { connect } from 'react-redux'
import { actionTogglePanel } from '../../../redux/actions/panelAction'
import { Button } from 'reactstrap';
class LabelItem extends Component {
    handleClick = () => {
        this.props.actionTogglePanel({
            id:this.props.i,
            width: parseInt(this.props.width),
            height: parseInt(this.props.height),
            left: parseInt(this.props.left),
            top: parseInt(this.props.top),
            margin: parseInt(this.props.margin),
            backgroundColor: this.props.backgroundColor,
            padding: parseInt(this.props.padding),
            borderRadius: parseInt(this.props.borderRadius),
            boxShadow1:  parseInt(this.props.boxShadow1),
            boxShadow2:  parseInt(this.props.boxShadow2),
            boxShadow3:  parseInt(this.props.boxShadow3),
            boxShadowColor:  this.props.boxShadowColor,
            zIndex: 0,
        })
    }
    render() {
        const { i, width, height, left, top } = this.props;
        return (
            <div>
                <div className="card">
                    <Button color="primary" className="px-4" onClick={this.handleClick}>Choose</Button>
                    <div className="card-header" id="note1">
                        <h6 className="mb-0">
                            <a data-toggle="collapse" data-parent="#noteList" href={"#noteContent" + i} aria-expanded="true" aria-controls="noteContent1">
                                left: {left} - top: {top}
                            </a>
                        </h6>
                    </div>
                    <div id={"noteContent" + i} className="collapse in" role="tabpanel" aria-labelledby="note1">
                        <div className="card-body">
                            width:{width} - height:{height}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
const mapStateToProps = (state, ownProps) => {
    return {
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        actionTogglePanel: (i) => {
            dispatch(actionTogglePanel(i))
        },
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(LabelItem);